/*
#styleguide
Article - Title

```
<h2 class="articleTtl"></h2>
<h3 class="articleH3"></h3>
<h4 class="articleH4"></h4>
```
*/
/* article - title */
.articleTtl{
    font-size: 32px;
    line-height: 1.25;
    margin-top: 30px;
    margin-bottom: 30px;
    text-align: center;
    font-weight: normal;
    &:first-child{
        margin-top: 0;
    }
    &.articleTtl_bd{
        padding-bottom: 30px;
        margin-bottom: 0;
        border-bottom: 1px solid $gray-light;
    }
    &.articleTtl_03{
        font-size: 24px;
        margin-top: 30px;
        padding: 15px;
        margin-top: 40px;
        background: #ebebeb;
        // border-top: 1px solid $gray;
    }
}
.articleTtl_03[aria-selected="true"] {
    .lstAcc-descTermSwitch::before {
        content: "";
    }
    .lstAcc-descTermSwitchIcon {
        transform: rotate(180deg);
    }
}
.articleTtl_03[aria-selected="false"] {
    .lstAcc-descTermSwitch::before {
        content: "";
    }
}

.articleH3{
    display: flex;
    align-items: stretch;
    font-size: 18px;
    font-weight: bold;
    line-height: 24px;
    margin-top: 30px;
    margin-bottom: 20px;
    clear: both;
    &::before {
        display: block;
        content: "";
        width: 3px;
        margin-right: $base-space;
        background-color: $brand-primary;
    }
}
.articleH4{
    font-size: 16px;
    font-weight: 600;
    line-height: 24px;
    margin-top: 20px;
    margin-bottom: 10px;
}

// for products/index.html
body.page_products h2.articleTtl{
    border-top: 1px solid $gray;
    border-bottom: 1px solid $gray;
    padding: 10px;
}
body.page_products .articleH3{
    text-align: center;
    margin-bottom: 16px;
    color: $brand-primary;
    padding: 10px 0;
    display: block;
    &::before {
        display: inline-block;
        content: "";
        width: 72px;
        height: 1px;
        margin-right: 16px;
        background-color: $brand-primary;
        vertical-align: middle;
    }
    &::after {
        display: inline-block;
        content: "";
        width: 72px;
        height: 1px;
        margin-left: 16px;
        background-color: $brand-primary;
        vertical-align: middle;
    }
}

// for link/internal.html
.articleTtl-link{
    font-size: 32px;
    line-height: 1.25;
    margin-bottom: 30px;
    color: $brand-primary;
}
.articleJump-link{
    font-weight: bold;
    margin-bottom: 10px;
}
.articleTxt-link{}
.articleBtn-link{
    text-align: center;
    margin-top: 30px;
    a{
        border: 1px solid $brand-primary;
        display: inline-block;
        padding: 5px 15px;
        border-radius:4px ;
        color: $brand-primary;
        text-decoration: none;
        width: 320px;
        max-width: 100%;
        margin: 10px 8px;
        -moz-box-sizing: border-box;
        -webkit-box-sizing: border-box;
        -o-box-sizing: border-box;
        box-sizing: border-box;
        &:hover{
          background: $brand-primary;
          color: $white;
        }
        &:after{
          color: inherit;
          font-family: rex-icon;
          content: "\F127";
          font-size: 14px;
          text-decoration: none;
          margin-left: 5px;
        }
    }
}

// for error404.html
.articleTtl-error{
    font-size: 32px;
    line-height: 1.25;
    margin-bottom: 30px;
    text-align: center;
}
.articleTxt-error{
    text-align: center;
}
.articleBtn-error{
  text-align: center;
  margin-top: 30px;
  a{
        border: 1px solid $brand-primary;
        display: inline-block;
        padding: 5px 15px;
        border-radius:4px ;
        color: $brand-primary;
        text-decoration: none;
        width: 320px;
        max-width: 100%;
        margin: 0 8px;

        -moz-box-sizing: border-box;
        -webkit-box-sizing: border-box;
        -o-box-sizing: border-box;
        box-sizing: border-box;
        &:hover{
          background: $brand-primary;
          color: $white;
        }
        &:after{
          color: inherit;
          font-family: rex-icon;
          content: "\F127";
          font-size: 14px;
          text-decoration: none;
          margin-left: 5px;
        }
  }
}

/*
#styleguide
Article - Lead / Table Of Contents

```
<p class="articleLead"></p>
<h2 class="articleToc"></h2>
```
*/
/* article - lead */
.articleLead{
  font-weight: bold;
  margin-top: 20px;
  margin-bottom: 20px;
}

/* article - table of contents() */
.articleToc{
  font-weight: bold;
  font-size: 24px;
  margin-top: 10px;
  margin-bottom: 30px;
}

/* article - detail */
.articleDetail{
  padding: 30px 0;
  border-top: 1px solid $gray;
  border-bottom: 1px solid $gray;
  .articleDetail-date{
    margin-bottom: 20px;
  }
  .articleDetail-ttl{
    font-size: 32px;
    margin-bottom: 20px;
    font-weight: bold;
  }
  .articleDetail-cat{
    margin-bottom: 20px;
    color: $brand-primary;
    a{
      color: inherit;
    }
  }
  .articleDetail-lead{
    font-size: 20px;
    font-weight: bold;
  }
}

/*
#styleguide
Article - List

```
<div class="articleList">
        <div class="articleList-date">yyyy/mm/dd</div>
        <div class="articleList-ttl"><a href="#"></a></div>
        <div class="articleList-cat"><a href="#"></a></div>
        <i aria-hidden="true" class="rex-icon chevron-right"></i>
<!-- /.articleList --></div>
<div class="articleList">
        <div class="articleList-date">yyyy/mm/dd</div>
        <div class="articleList-ttl"><a href="#"></a></div>
        <div class="articleList-cat"><a href="#"></a></div>
        <i aria-hidden="true" class="rex-icon chevron-right"></i>
<!-- /.articleList --></div>
<div class="articleList">
        <div class="articleList-date">yyyy/mm/dd</div>
        <div class="articleList-ttl"><a href="#"></a></div>
        <div class="articleList-cat"><a href="#"></a></div>
        <i aria-hidden="true" class="rex-icon chevron-right"></i>
<!-- /.articleList --></div>
```
*/
/* article - lead */
/* article -list */
.bg_gray .articleList{
    display: block;
}
.articleList{
    display: block;
    padding: 30px 50px 30px 0;
    color: $gray-darkest;
    position: relative;
    border-bottom: 1px solid $gray-light;
    &:hover{
        text-decoration:none;
        background-color: $gray-lightest;
    }
    .rex-icon{
        position: absolute;
        top: 50%;
        right: 0;
        line-height: 1;
        margin-top: -0.5em;
    }
    .articleList-link{}
    .articleList-date{
        margin-bottom: 10px;
    }
    .articleList-ttl{
        margin-bottom: 10px;
        font-weight: bold;
        a{
            color: inherit;
            display: block;
        }
    }
    .articleList-cat{
        margin-top: 10px;
        color: $brand-primary;
        a{
            color: inherit;
        }
    }
}

/* article - img */
.articleImg{
    text-align: center;
    margin-top: 30px;
    img{
        max-width:100%;
        height: auto;
    }
}
.articleImgTxt{
  text-align: center;
}

/*
#styleguide
Article - Box

```
<div class="articleBox">
    <p class="articleBox-img"><img src="https://placehold.jp/134x134.png" alt=""></p>
    <p class="articleBox-ttl"></p>
    <p class="articleBox-cat"></p>
    <p class="articleBox-txt"></p>
    <p class="articleBox-link"><a href="#"><i aria-hidden="true" class="rex-icon chevron-right"></i></a></p>
<!-- /.articleBox --></div>
```
*/

/* article - box */
.articleBox{
    border: 1px solid $gray-medium;
    padding: 20px;
    margin-top: 30px;
    .articleBox-img{
        float: left;
        margin-right: 20px;
    }
    .articleBox-ttl{
        font-weight: bold;
        overflow: hidden;
    }
    .articleBox-cat{
        color: $brand-primary;
        overflow: hidden;
    }
    .articleBox-txt{
        margin-top: 20px;
        overflow: hidden;
    }
    .articleBox-link{
        overflow: hidden;
        text-align: right;
        .rex-icon{
            font-size: 16px;
            margin-left: 5px;
        }
    }
}

/*
#styleguide
Article - Card

```
<div class="articleCard">
    <div class="articleCard-img">
        <img src="/media/assets/common/img/img_thum_004.png" alt="">
    </div>
    <div class="articleCard-inner">
        <div class="articleCard-top">
            <div class="articleCard-cat"><a href="#"></a></div>
                <div class="articleCard-tag"><a href="#" class="tag-parts"></a><a href="#" class="tag-parts"></a></div>
            </div>
            <div class="articleCard-ttl"><a href=""></a></div>
    <!-- /.articleCard-inner --></div>
<!-- /.articleCard --></div>
```
*/
/* article -card */
.articleCard{
    box-shadow: 0 5px 10px 0px rgba(0,0,0,.2);
    border-radius: 3px;
    overflow: hidden;
    height: 100%;
}
.articleCard-img{
    img{
        width: 100%;
        height: auto;
        cursor:pointer;
        -webkit-transition: all 0.3s ease;
        -moz-transition: all 0.3s ease;
        -o-transition: all 0.3s ease;
        transition: all 0.3s ease;
        &:hover{
            opacity: 0.7;
        }
    }
}
.articleCard-inner{
    padding: 20px;
}
.articleCard-top{
    overflow: hidden;
    margin-bottom: -5px;
}
.articleCard-bottom{
    margin-top: 10px;
    overflow: hidden;
}
.articleCard-cat{
    color: $brand-primary;
    float: left;
    margin-right: 10px;
    margin-bottom: 5px;
    a{
        color: $brand-primary;
    }
}
.articleCard-tag{
    float: left;
    margin-right: -10px;
    margin-top: -5px;
    margin-bottom: 10px;
}
.articleCard-tag{
    .tag-parts{
        margin-right: 10px;
        margin-top: 5px;
    }
}

.articleCard.articleCard_new .articleCard-ttl:before{
    content:"NEW";
    border: 1px solid $brand-primary;
    color: $brand-primary;
    border-radius: 4px;
    font-size: 10px;
    line-height: 1;
    padding: 5px 8px;
    margin-right: 8px;
    // position: absolute;
    // left: 0;
    // top: -25px;
    display: inline-block;
    vertical-align: middle;
    pointer-events: none;
}
.articleCard-ttl{
    margin-top: 15px;
    font-weight: bold;
    position: relative;
    &:first-child{
        margin-top: 0;
    }
    a{
        color: $gray-darkest;
        display:inline;
        vertical-align: middle;
    }
}
.articleCard-lead{
    margin-top: 10px;
    a{
        color: $gray-darkest;
    }
}

@media screen and (max-width: 756px){
    .articleLead{
        margin-bottom: 10px;
    }
    .articleH3{
        margin-top: 25px;
        margin-bottom: 10px;
    }
    .articleH4{
        margin-top: 15px;
        margin-bottom: 5px;
    }
    .articleBox{
        padding: 15px;
    }
    .articleBox .articleBox-img{
        width: 33%;
        margin-right: 15px;
    }
    .articleTtl{
        font-size: 23px;
        margin-top: 30px;
        margin-bottom: 15px;
    }
    .articleImg{
        margin-top: 20px;
        .ytMovie{
            width:100%;
            padding-bottom: 56.25%;
            height:0px;
            position: relative;
            display: block;
            iframe{
                position: absolute;
                top: 0;
                left: 0;
                width: 100%;
                height: 100%;
            }
        }
    }
    .articleCard-inner{
        padding: 15px;
    }
    .articleCard-lead{
        display: none;
    }
    .articleCard-ttl{
        margin-top: 10px;
    }
    .articleCard-img img{
        object-fit: cover;
        width: 100%;
        height: 100%;
    }
    .articleTtl-error{
      font-size: 24px;
      margin-bottom: 20px;
    }
    .articleTtl.articleTtl_bd{
      padding-bottom: 15px;
    }
    .articleDetail .articleDetail-date{
      margin-bottom: 10px;
    }
    .articleList .articleList-link{
      padding: 20px 30px 20px 0;
    }
    .articleBox .articleBox-txt{
      margin-top: 10px;
    }
    .articleDetail .articleDetail-cat{
      margin-bottom: 10px;
    }
    .articleDetail{
      padding: 20px 0;
    }
    .articleDetail .articleDetail-ttl{
      font-size: 16px;
      margin-bottom: 10px;
    }
    .articleTtl.articleTtl_03{
        margin-top: 30px;
        margin-bottom: 0;
        padding: 15px;
        background: $gray-lighter;
        border: none;
        font-size: 16px;
        text-align: left;
        &:first-child{
            margin-top: 0;
        }
    }
}

@media screen and (max-width: 320px){
    .articleTtl{
        font-size: 21px;
    }
    .articleBtn-link{
        a {
            margin: 10px 0;
        }
    }
    .articleBtn-error a {
        margin: 0;
    }
}
