.alert {
    position: fixed;
    top: 0;
    left: 0;
    z-index: 10020;
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 100%;
    padding: 11.5px 20px 11.5px 18px;
    word-break: break-all;
    border-radius: $base-radius;
    box-sizing: border-box;
    transition: margin-top .2s $acceleration-curve;
    &.alert_success {
        color: $system-success-txt;
        background-color: $system-success-bg;
        border: 1px solid $system-success-stroke;
    }
    &.alert_info {
        color: $system-info-txt;
        background-color: $system-info-bg;
        border: 1px solid $system-info-stroke;
    }
    &.alert_warning {
        color: $system-warning-txt;
        background-color: $system-warning-bg;
        border: 1px solid $system-warning-stroke;
    }
    &.alert_danger {
        color: $system-danger-txt;
        background-color: $system-danger-bg;
        border: 1px solid $system-danger-stroke;
    }
    .rex-icon {
        margin-right: 22px;
    }
    .alert-txt {
        font-size: 16px;
        font-weight: 400;
        line-height: 32px;
    }
}
.alert-btnClose {
    margin-left: auto;
    color: inherit;
    background-color: transparent;
    -webkit-appearance: none;
    padding: 0;
    border-width: 0px;
    border-style: initial;
    border-color: initial;
    border-image: initial;
    cursor: pointer;
    .rex-icon {
        font-size: 19px;
        margin-right: 0;
        vertical-align: middle;
    }
    &:focus {
        outline: none;
    }
}

