.alertDesc {
    word-break: break-all;
    border-radius: $base-radius;
    padding: 10px 18px;
    box-sizing: border-box;
    &.alertDesc_danger {
        color: $system-danger-txt;
        background-color: $system-danger-bg;
        border: 1px solid $system-danger-stroke;
    }
}
.alertDesc-term {
    font-size: 16px;
    font-weight: 600;
    line-height: 32px;
    margin-bottom: 4px;
}
.alertDesc-dfnLstItem {
    display: flex;
    align-items: center;
    margin-bottom: 4px;
    &:last-child {
        margin-bottom: 0;
    }
    .rex-icon {
        font-size: 16px;
        margin-right: 14px;
    }
}
.alertDesc-txt {
    font: 400 16px/32px 'Hiragino Kaku Gothic ProN', Meiryo, MS-PGothic, sans-serif;
}
