/*
#overview
Text

font-size: 16px;
font-weight: 400;


1 bodyline-height: 1.7;
2 body    font-family: 'Hiragino Kaku Gothic ProN', Meiryo, MS-PGothic, sans-serif;
*/

.txt {
	font-size: 16px;
    font-weight: 400;
    &.txt_bold {
        font-weight: 600;
    }
}
/*
#styleguide
Text


```
<p class="txt">199720</p>
```

*/

/*
#styleguide
Text - Bold


```
<p class="txt txt_bold">199720</p>
```
*/

/*
#styleguide
Text - Lead

```
<p class="txt txt_lead">
    <br />
    
</p>
```

*/
.txt_lead {
    font-size: 20px;
    font-weight: 600;
    line-height: 30px;
    text-align: center;
}

/*
#styleguide
Text - Lead Small

```
<p class="txt txt_leadSmall">
    <br />
    
</p>
```

*/
.txt_leadSmall {
    font-size: 16px;
    font-weight: 600;
    line-height: 24px;
    text-align: center;
}

/*
#styleguide
Text - Overline

```
<p><small class="txt txt_overline">2-2018-068(2018.6.1)</small></p>
<p><small class="txt txt_overline"> Rakuten</small></p>
```

*/
.txt_overline {
    font-size: 10px;
    font-weight: 400;
    line-height: 15px;
}

/*
#styleguide
Text - Caption

```
<p class="txt txt_caption">2018914</p>
<p class="txt txt_caption">9:0019:00 9:0017:00</p>
```

*/
.txt_caption {
    font-size: 12px;
    font-weight: 400;
    line-height: 18px;
}
