/*
#styleguide
Label

```
<span class="label">
    CM
</span>
```
*/
.label {
    display: inline-block;
    padding: 6px 8px;
    color: $brand-primary;
    font-size: 12px;
    font-weight: 400;
    line-height: 12px;
    border-radius: $base-radius;
    background-color: $white;
    box-sizing: border-box;
}


/*
#styleguide
Label - OutLine

```
<span class="label label_outLine">
    
</span>
```
*/
.label {
    &.label_outLine {
        border: 1px solid $brand-primary;
        background-color: transparent;
    }
}


/*
#styleguide
Label - Baloon

```
<span class="label label_baloon">
    
</span>
```
*/
.label {
    &.label_baloon {
        position: relative;
        color: $white;
        background-color: $brand-primary;
        &::after {
            position: absolute;
            bottom: -10px;
            left: 50%;
            content: "";
            border: 5px solid transparent;
            border-top: 5px solid $brand-primary;
            transform: translateX(-50%);
        }
    }
}


/*
#styleguide
LabelCircle

```
<span class="labelCircle">
    <br />
</span>
```
*/
.labelCircle {
    display: inline-flex;
    align-items: center;
    justify-content: center;
    width: 56px;
    height: 56px;
    font-size: 16px;
    line-height: 20px;
    color: $white;
    border-radius: 28px;
    background-color: $brand-primary;
}
