        }
    }

    .spOnly.gnaviDropBox {
        position: fixed;
        top: 0;
        left: 0;
        z-index: 10000;
        width: 100%;
        height: calc(100% - 57px);
        margin-top: 57px;
        background-color: #fff;
        overflow-y: scroll;
        -webkit-transform: translateX(-100%);
        transform: translateX(-100%);
    }
    #grpheader .header-btn {
      display: block;
      width: auto;
      height: auto;
      margin-left: 0;
      margin-right: 20px;
      padding: 0;
      text-align: center;
    }
    .header-btn_icon .rex-icon.x{
      display: block;
    }
    .header-btn_icon .rex-icon[aria-selected=true]{
      display: block;
    }
    .header-btn_icon .rex-icon[aria-selected=false]{
      display: none;
    }
    .header-btn_icon .header-btn_txt {
      display: block;
      font-size: 10px;
    }
    #grpheader .rc-h-gnavi .rc-h-inner > li > a {
      &::after {
          bottom: 0;
      }
    }

    #grpheader.rc-h-simple {
      .rc-h-service-bar-inner {
        height: 62px;
      }
      .rc-h-inner {
        height: 62px;
      }
      .rc-h-site-id {
        justify-content: center;
        width: 100%;
        height: 62px;
      }
      .rc-h-txt-wrap {
        margin-top: 63px;
      }
    }
}

