/*
#styleguide
Filter

```
<div class="filter">
    <h2 class="filter-ttl"></h2>
    <div class="filter-btn-wrap">
        <a href="#" class="filter-btn"></a>
        <a href="#" class="filter-btn"></a>
        <a href="#" class="filter-btn"></a>
    </div>
<!-- /.filter --></div>
```
*/
.filter{
  .filter-ttl{
    font-size: 24px;
    line-height: 1.25;
    font-weight: bold;
    margin-bottom: 15px;
    text-align: center;
  }
  .filter-btn-wrap{
    text-align: center;
    letter-spacing: -0.4em;
  }
  .filter-btn{
      display: inline-block;
      font-size: 16px;
      line-height: 16px;
      margin: 5px;
      padding: .75em 1em;
      text-decoration: none;
      text-align: center;
      vertical-align: baseline;
      color: $brand-primary;
      letter-spacing: normal;
      background-color: $white;
      border: 1px solid $brand-primary;
      border-radius: $base-radius;
      box-sizing: border-box;
      transition: background-color .2s $standard-curve, color .2s $standard-curve;
      &:hover, &:focus {
          text-decoration: none;
          color: $white;
          background-color: $brand-light;
          border: 1px solid $brand-light;
      }
      &:active {
          color: $white;
          background-color: $brand-darker;
          border: 1px solid $brand-darker;
      }
      &.btn[disabled] , &.btn_disabled {
          color: $gray-light;
          background-color: $gray-lighter;
          border: 1px solid transparent;
          cursor: not-allowed;
          &:hover, &:focus, &:active {
              background-color: $gray-lighter;
              border: 1px solid transparent;
          }
      }
      &.btn_disabled {
          pointer-events: none;
      }
  }
}

@media screen and (max-width: 756px){
    .filter .filter-ttl{
      font-size: 16px;
    }
}

