/*
#styleguide
Tag

```
<button data-trigger="removedTag" class="tag">
    <span class="tag-txt"></span>
    <span class="tag-btn"><i aria-hidden="true" class="rex-icon x"></i></span>
</button>
```
*/
.tag {
    display: inline-flex;
    align-items: center;
    height: 24px;
    padding: 6px 8px;
    color: $white;
    border: 0;
    border-radius: $base-radius;
    background-color: #c4c4c4;
    box-sizing: border-box;
    cursor: pointer;
    -webkit-appearance: none;
    -moz-appearance: none;
    appearance: none;
    transition: background-color .2s $standard-curve, color .2s $standard-curve;
    .tag-txt {
        margin-right: 8px;
        font-size: 12px;
        font-weight: 400;
        line-height: 12px;
    }
    @media all and (-ms-high-contrast:none) { // IE11
        *::-ms-backdrop, .tag-txt {
            margin-top: 3px;
        }
    }
    .tag-btn {
        line-height: 7px;
        .rex-icon {
            font-size: 7px;
        }
    }
    &:hover , &:focus {
        color: $gray;
        background-color: #e5e5e5;
    }
    &:active {
        color: $white;
        background-color: #c4c4c4;
    }
}

/*
#styleguide
TagSort

```
<label class="tagSort" data-trigger="sortTag">
    <input type="checkbox" value="" class="tagSort-checkbox">
    <span Class="tagSort-parts"></span>
</label>
```
*/

.tag-parts {
    display: inline-block;
    padding: 2px 10px;
    font-family: 'Hiragino Kaku Gothic ProN', Meiryo, MS-PGothic, sans-serif;
    font-size: 12px;
    font-weight: 400;
    color: $white;
    border-radius: 100px;
    background-color: $gray;
    box-sizing: border-box;
    transition: background-color .2s $standard-curve;
    &:hover , &:focus {
        background-color: $brand-light;
        text-decoration: none;
    }
}
.tagSort {
    cursor: pointer;
    .tagSort-parts {
        @extend .tag-parts;
    }


    .tagSort-checkbox {
        display: none;
    }

    @media all and (-ms-high-contrast:none) { // IE11
        *::-ms-backdrop, .tagSort-parts {
            padding: 8px 8px 4px;
        }
    }
    &:hover , &:focus {
        .tagSort-parts {
            background-color: $brand-light;
        }
    }
    .tagSort-checkbox:checked + .tagSort-parts {
        background-color: $brand-primary;
    }
}
