.containerWrap {
    width: 1280px;
    margin: 0 auto;
}

.containerWrap_col02,
.containerWrap_col03 {
    display: flex;
    justify-content: space-between;
}

.container {}

.container_tile {
    position: relative;
    display: block;
    width: 640px;
    height: 400px;
    &Col02 {
        position: relative;
        display: block;
        width: 624px;
        height: 400px;
    }
    &Col03 {
        position: relative;
        display: block;
        width: 405px;
        height: 400px;
    }
}

.container_tile,
.container_tileCol02,
.container_tileCol03 {
    .container-descLst {
        position: absolute;
        top: 0;
        left: 50%;
        width: 100%;
        z-index: 1;
        color: $gray-darker;
        text-align: center;
        box-sizing: border-box;
        transform: translateX(-50%);
    }
    .container-descLstTerm {
        margin-bottom: 4px;
        padding-top: 24px;
        font-size: 32px;
        font-weight: 600;
        line-height: 32px;
    }
    .container-descLstTermSub {
        display: block;
        margin-bottom: 16px;
        font-size: 16px;
        font-weight: 600;
        line-height: 16px;
        color: $brand-primary;
    }
    .container-descLstTermMain {
        display: flex;
        justify-content: center;
    }
    .container-descLstTermMainLabel {
        display: block;
        font-size: 14px;
        font-weight: 400;
        line-height: 14px;
        margin-right: 8px;
        padding: 7px 0;
        border-top: 1px solid $gray-darkest;
        border-bottom: 1px solid $gray-darkest;
        vertical-align: middle;
    }
    .container-descLstTermMainTxt {
        display: block;
        font-size: 32px;
        font-weight: 600;
        line-height: 32px;
        vertical-align: middle;
        &.container-descLstTermMainTxt_small {
            display: block;
            font-size: 24px;
            font-weight: 600;
            line-height: 24px;
            vertical-align: middle;
        }
    }
    .container-descLstDfn {
        font-size: 16px;
        font-weight: 400;
        line-height: 24px;
    }
    .container-descLstDfnSup {
        top: 0;
        font-size: 10px;
        line-height: 10px;
        vertical-align: text-top;
    }
    .container-btn {
        display: inline-block;
        font-size: 16px;
        font-weight: 600;
        line-height: 16px;
        margin: 12px 16px 16px 16px;
        padding: .75em 1em;
        text-decoration: none;
        text-align: center;
        vertical-align: baseline;
        font-family: 'Hiragino Kaku Gothic ProN', Meiryo, MS-PGothic, sans-serif;
        color: $gray-dark;
        border: 1px solid $gray-dark;
        border-radius: $base-radius;
        background-color: transparent;
        box-sizing: border-box;
        cursor: pointer;
        transition: background-color .2s $standard-curve, color .2s $standard-curve;
    }
    /* IE11 */
    @media all and (-ms-high-contrast:none) {
        *::-ms-backdrop, .container-btn {
            padding: calc(.75em + 2px) 1em calc(.75em - 2px);
        }
    }
    .container-fig {
        overflow: hidden;
    }
    .container-img {
        width: 100%;
        height: auto;
        vertical-align: bottom;
        transition: transform .2s $standard-curve;
    }
    .container-lst {
        position: absolute;
        bottom: 8px;
        right: 8px;
    }
    .container-lst_asterisk {
        list-style: none;
        padding-left: 0;
    }
    .container-lst_asterisk .container-lstItem {
        position: relative;
        padding-left: 1em;
        font-size: 12px;
        font-weight: 400;
        color: $gray-darker;
        line-height: 12px;
    }
    .container-lst_asterisk .container-lstItem::before {
        position: absolute;
        top: 0;
        left: 0;
        display: block;
        content: "";
    }
    &:hover , &:focus {
        .container-btn {
            color: $white;
            background-color: $gray-medium;
            border: 1px solid $gray-medium;
        }
        .container-img {
            transform: scale(1.1, 1.1);
        }
    }
    &:active {
        .container-btn {
            color: $white;
            background-color: $gray-darkest;
            border: 1px solid $gray-darkest;
        }
        .container-img {
            transform: scale(1.1, 1.1);
        }
    }
}

