.container_foil {
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 1280px;
    height: 344px;
    margin: 0 auto;
    padding: 0 104px;
    background-color: $gray-lighter;
    box-sizing: border-box;
    .container-descLst {
        width: 50%;
        color: $gray-darker;
        text-align: center;
    }
    .container-descLstTerm {
        margin-bottom: 14px;
    }
    .container-descLstTermSup {
        top: 0;
        font-size: 12px;
        line-height: 12px;
        vertical-align: top;
    }
    .container-descLstTermMain {
        display: block;
        margin-bottom: 12px;
        font-size: 36px;
        font-weight: 600;
        line-height: 36px;
    }
    .container-descLstTermSub {
        display: block;
        margin-bottom: 12px;
        font-size: 24px;
        font-weight: 400;
        line-height: 24px;
    }
    .container-descLstTermSub_small {
        margin-bottom: 0;
        font-size: 14px;
        font-weight: 600;
        line-height: 14px;
        color: $life-primary;
    }
    .container-descLstDfn {
        display: inline-block;
        text-align: left;
    }
    .container-descLstDfnTxt {
        font-size: 16px;
        font-weight: 400;
        line-height: 32px;
    }
    .container-fig {
        width: 50%;
    }
    .container-img {
        vertical-align: bottom;
    }
    .container-lst {
        margin-top: 14px;
    }
    .container-lst_asterisk {
        list-style: none;
        padding-left: 0;
    }
    .container-lst_asterisk .container-lstItem {
        position: relative;
        padding-left: 1em;
        font-size: 12px;
        font-weight: 400;
        color: $gray-darker;
        line-height: 12px;
    }
    .container-lst_asterisk .container-lstItem::before {
        position: absolute;
        top: 0;
        left: 0;
        display: block;
        content: "";
    }
}

