.containerWrap.containerWrap_forPickUp {
    display: flex;
    justify-content: space-between;
    width: 800px;
    margin: 0 auto;
}
.container_card {
    display: flex;
    flex-direction: column;
    width: 174px;
    text-decoration: none;
    color: $gray-darkest;
    border: 1px solid $gray;
    .container-thumb {}
    .container-thumbImg {
        width: 174px;
        height: 127px;
    }
    .container-dscLst {
        padding: 8px 8px 0;
        overflow: hidden;
    }
    .container-dscLstTerm {
        margin-bottom: 4px;
        font-size: 16px;
        font-weight: 600;
        line-height: 24px;
        color: $brand-primary;
        text-align: center;
    }
    .container-dscLstDfn {
        font-size: 16px;
        font-weight: 400;
        line-height: 24px;
        color: $gray-darker;
    }
    .container-lnk {
        margin-top: auto;
        padding: 0 8px 8px;
        text-align: right;
        color: $lnk-txt;
        transition: color .2s $standard-curve;
    }
    &:focus , &:hover {
        text-decoration: none;
        .container-lnk {
            color: $brand-light;
        }
    }
    &:active {
        .container-lnk {
            color: $brand-darker;
        }
    }
}

@media screen and (max-width: 756px){
    .containerWrap{width: 100%;}
    .containerWrap_col02, .containerWrap_col03{display: block;}
    .container_tileCol02{width: 100%; height: auto; margin-top: 15px;}
    .container_tileCol03{width: 100%; height: auto; margin-top: 15px;}
    .container_tileCol02 .container-descLstTermMainTxt{font-size: 24px;}
    .container_tile .container-descLstTermMainLabel,
    .container_tileCol02 .container-descLstTermMainLabel,
    .container_tileCol03 .container-descLstTermMainLabel{display: none;}
    .container-fig .container-img{ width: 100%; height: auto; margin-top: 0;}
    .containerWrap.containerWrap_forPickUp{ display: block; width: auto;margin-right: -10px;}
    .containerWrap.containerWrap_forPickUp .containerWrap-item{ width: calc(50% - 10px); margin-right: 10px; float: left;}
    .containerWrap.containerWrap_forPickUp .container-thumb img{ width: 100%; height: auto;}
    .container-thumb{text-align: center;}
    .container_card{display: block; width: auto;}
    .containerWrap.containerWrap_forPickUp{
        overflow: hidden;
        margin-top: 10px;
    }
    .containerWrap.containerWrap_forPickUp li{margin-top: 15px;}
    .container_card {
        &:hover {
            text-decoration: none;
            .container-lnk {
                color: $lnk-txt;
            }
        }
    }
}

@media screen and (max-width: 320px){
    .container_tileCol02 .container-descLstTermMainTxt{font-size: 22px;}
    .container_card .container-dscLstTerm{font-size: 15px;}
}
