.searchForm {
    position: relative;
    display: inline-block;
    width: 100%;
    height: 42px;
}
.searchForm-bar {
    padding: 11px 73.5px 11px 18px;
    width: 100%;
    height: 42px;
    font-family: 'Hiragino Kaku Gothic ProN', Meiryo, MS-PGothic, sans-serif;
    font-size: 16px;
    line-height: 16px;
    font-weight: 400;
    color: $gray-darkest;
    border: 1px solid $gray-light;
    border-radius: 30px;
    background-color: $white;
    transition: color .2s $standard-curve, border .2s $standard-curve;
    box-sizing: border-box;
    appearance: none;
    &::-ms-clear {
        visibility:hidden;
    }
}
.searchForm-bar:placeholder-shown {
    color: $gray-medium;
}
/* Safari */
.searchForm-bar::-webkit-input-placeholder {
    line-height: normal;
}
/* IE */
.searchForm-bar:-ms-input-placeholder {
    color: $gray-medium;
}
/* Edge */
.searchForm-bar::-ms-input-placeholder {
    color: $gray-medium;
}
.searchForm-bar:focus {
    border-color: $brand-primary;
    border-radius: 30px;
    background-color: #f9f9f9;
    outline: none;
}
.searchForm-btn {
    display: inline-block;
    position: absolute;
    text-align: center;
    vertical-align: bottom;
    white-space: nowrap;
    touch-action: manipulation;
    background-image: none;
    border: 1px solid transparent;
    outline: none;
    cursor: pointer;
    box-sizing: border-box;
    appearance: none;
}
.searchForm-btn .rex-icon {
    font-size: 18px;
    vertical-align: top;
}
.searchForm-btn_submit {
    position: absolute;
    top: 0;
    right: 0;
    height: 42px;
    padding: 11px 16px 11px 12px;
    color: $white;
    border-radius: 0 30px 30px 0;
    background-color: $brand-primary;
    transition: background-color .2s $standard-curve, color .2s $standard-curve;
}
.searchForm-btn_submit:hover,
.searchForm-btn_submit:focus {
    background-color: $brand-light;
}
.searchForm-btn_submit:active {
    position: absolute;
    background-color: $brand-darker;
}
.searchForm-btn_reset {
    display: none;
    position: absolute;
    top: 12px;
    right: 60px;
    margin: 0;
    padding: 0;
    background-color: transparent;
}
.searchForm-btn_reset:active {
    position: absolute;
}
.searchForm-btn_reset[aria-selected="true"] {
    display: block;
}
