.modalWrap {
    position: fixed;
    top: 0;
    left: 0;
    z-index: 10010;
    width: 100%;
    height: 100vh;
}
.overLay {
    background: rgba(0,0,0,.3);
    height: 100vh;
    left: 0;
    position: absolute;
    top: 0;
    width: 100%;
}

body[aria-busy="true"] .modalWrap {
    animation-name: modalOpen;
    animation-duration: .3s;
    animation-fill-mode: forwards;
}
body[aria-busy="false"] .modalWrap {
    animation-name: modalClose;
    animation-duration: .2s;
    animation-fill-mode: forwards;
}
@keyframes modalOpen {
    0% {
        opacity: 0;
    }
    100% {
        opacity: 1;
    }
}

@keyframes modalClose {
    0% {
        opacity: 1;
    }
    100% {
        opacity: 0;
    }
}
.modal {
    position: absolute;
    top: 50%;
    left: 0;
    right: 0;
    z-index: 10011;

    font-family: 'Hiragino Kaku Gothic ProN', Meiryo, MS-PGothic, sans-serif;
    background-color: $white;
    transform: translate(0,-50%);
}
.modal-body {
    position: relative;

    max-width: 1070px;
    margin: 0 auto;


    &>.modalClose{
        position:  absolute;
        right: 0;
        top: -40px;

        color: #fff;
        .rex-icon.x{
            font-size: 36px;
        }


    }
}
.modal-bodyInner {
    padding: 30px 10px;

}
.modal-bodyInner .modal-bodyInner-ttl{
    margin-top: -10px;
}
.modal-bodyInner .modal-bodyInner-con{
    overflow-y: auto;
    max-height: 65vh;
}
.modal-bodyInner .modal-ttl {
    font-size: 26px;
    font-weight: bold;
    margin-bottom: 10px;
}
.modal-bodyInner .qaBox-ttl{
    font-size: 18px;
    font-weight: bold;
}
.modal-bodyInner .qaBox-img{
    margin-top: 15px;
    height: 60px;
    padding-right: 10px;
    text-align: center;
    img{
        width: auto;
        height: 38px;
    }
}
.modal-bodyInner .qaBox-btn{
    margin-top: 10px;
    text-align: center;

    a{
      border: 1px solid $gray;
      display: inline-block;
      padding: 5px 12px;
      border-radius:4px ;
      color: $gray;
      text-decoration: none;
      &:hover{
        background: $gray;
        color: $white;
      }
    }
}

.contactBox{
    .contactBox-ttl{
        font-size: 18px;
        font-weight: bold;
        margin-bottom: 10px;
        &.contactBox-ttl_red{
            color: $brand-primary;
        }

    }
    .contactTop{
        font-size: 15px;
        margin-bottom: 10px;
        &:first-child{
            margin-top: 0;
        }
        .contactTop-ttl{
            font-weight: bold;
            margin-bottom: 5px;
        }
    }
    .contactTel{
        overflow: hidden;
        margin-bottom: 20px;
        &:last-child{
            margin-bottom: 0;
        }
        .contactTel-left{
            float: left;
            width:  calc(100% - 138px);
            .contactTel-num{
                font-size: 20px;
                font-weight: bold;
            }
            .contactTel-txt{
                font-size: 12px;
            }
        }
        .contactTel-right{
            float: right;
            width: 128px;
            padding-top: 2px;
            a{
              border: 1px solid $gray;
              display: inline-block;
              padding: 5px 15px;
              border-radius:4px ;
              color: $gray;
              text-decoration: none;
              &:hover{
                background: $gray;
                color: $white;
              }
            }
        }
    }
}

.modal-head {
    position: relative;
    padding: 16px 30px;
    text-align: center;
    border-bottom: 1px solid $gray;
    background-color: #f2f2f2;
}
.modal-head .modal-ttl {
    font-size: 24px;
    font-weight: 400;
    line-height: 24px;
}
.modal-head .rex-icon {
    position: absolute;
    top: 16px;
    right: 30px;
    font-size: 24px;
    vertical-align: bottom;
    cursor: pointer;
}
.modal-lead {
    font-size: 16px;
    text-align: center;
    margin-bottom: 20px;
}
.modal-btn {
    display: block;
    width: 176px;
    margin: 40px auto 0;
    padding: .75em 1em;
    font-family: 'Hiragino Kaku Gothic ProN', Meiryo, MS-PGothic, sans-serif;
    font-size: 16px;
    line-height: 16px;
    text-decoration: none;
    text-align: center;
    color: $gray-dark;
    vertical-align: baseline;
    background-color: $white;
    border: 1px solid $gray-dark;
    border-radius: $base-radius;
    box-sizing: border-box;
    cursor: pointer;
    transition: background-color .2s $standard-curve, color .2s $standard-curve;
    &:hover, &:focus {
        color: $white;
        background-color: $gray-medium;
        border: 1px solid $gray-medium;
    }
    &:active {
        color: $white;
        background-color: $gray-darkest;
        border: 1px solid $gray-darkest;
    }
    &.btn[disabled] , &.btn_disabled {
        color: $gray-light;
        background-color: $gray-lighter;
        border: 1px solid transparent;
        cursor: not-allowed;
        &:focus, &:active {
            background-color: $gray-lighter;
            border: 1px solid transparent;
        }
    }
    &.btn_disabled {
        pointer-events: none;
    }
}

.qaBox .columnRow_col3{
  margin-right: -20px;
}
.qaBox .columnRow_col3 .columnRowList{
  padding-right: 20px;
}

@media screen and (max-width: 756px){
    .modal{
      top: 56%;
    }

    .modal-bodyInner{
      padding: 15px 10px 20px;
    }
    .modalWrap{
      height: 90vh;
    }
    .modal-bodyInner .modal-ttl{
      font-size: 20px;
      margin-bottom: 10px;
    }
    .modal-bodyInner .qaBox-img{
      height: auto;
      width: 180px;
      margin-top: 5px;
    }
    .modal-bodyInner .qaBox-img img{
      height: 32px;
    }
    .contactBox .contactTel{
      margin-bottom: 20px;
    }
    .modal-bodyInner .modal-bodyInner-con{
        overflow-y: auto;
        max-height: 55vh;
    }
    .modal-body a.modalClose{
      display: inline-block;
    }
    .overLay{
      cursor: pointer;
    }
}

