.modal .info_link a .chevron-right {
    margin-left: auto;
}
.modal .info_link a {
    display: -webkit-box;
    display: -webkit-flex;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-align: center;
    -webkit-align-items: center;
    -ms-flex-align: center;
    align-items: center;
    padding: 9px 16px 10px;
    font-size: 16px;
    line-height: 24px;
    color: #006497;
    -webkit-transition: color .2s cubic-bezier(.25,0,.45,1);
    transition: color .2s cubic-bezier(.25,0,.45,1);
}
.modal .info_link a .sign-info-l {
    margin-right: 24px;
}
.modal .info_link_wrap {
    margin-top: 0;
    color: #006497;
    background-color: #ebf7fe;
    border: 1px solid #c1e6fa;
    border-radius: 4px;
}
.modal .info_link a:focus, .modal .info_link a:hover {
    color: #008CD4;
    text-decoration: none;
    outline: 0;
}
.modal .info_link a:active {
    color: #00496E;
    text-decoration: none;
    outline: 0;
}
@media screen and (max-width: 756px){
    .modal .alert .rex-icon {
        margin-right: 0;
        position: absolute;
        top: 50%;
        left: 16px;
        margin-top: -12px;
    }
    .modal .alert_info a {
        padding: 11.5px 40px 11.5px 64px;
    }
 }

