<i aria-hidden="true" class="service-icon service-icon_blankLifeActive"></i>
<i aria-hidden="true" class="service-icon service-icon_menu"></i>
```

*/
.service-icon {
    display: inline-block;
    background-image: url($spritesheet-image);
    background-size: $spritesheet-width $spritesheet-height;
    background-repeat: no-repeat;
    vertical-align: bottom;
    &.service-icon_bed {
        background-position: $service-icon-bed-offset-x $service-icon-bed-offset-y;
        width:$service-icon-bed-width;
        height:$service-icon-bed-height;
        &Hover {
            background-position: $service-icon-bed-hover-offset-x $service-icon-bed-hover-offset-y;
            width:$service-icon-bed-hover-width;
            height:$service-icon-bed-hover-height;
        }
        &Active {
            background-position: $service-icon-bed-active-offset-x $service-icon-bed-active-offset-y;
            width:$service-icon-bed-active-width;
            height:$service-icon-bed-active-height;
        }
    }
    &.service-icon_box {
        background-position: $service-icon-box-offset-x $service-icon-box-offset-y;
        width:$service-icon-box-width;
        height:$service-icon-box-height;
        &Hover {
            background-position: $service-icon-box-hover-offset-x $service-icon-box-hover-offset-y;
            width:$service-icon-box-hover-width;
            height:$service-icon-box-hover-height;
        }
        &Active {
            background-position: $service-icon-box-active-offset-x $service-icon-box-active-offset-y;
            width:$service-icon-box-active-width;
            height:$service-icon-box-active-height;
        }
    }
    &.service-icon_cancer {
        background-position: $service-icon-cancer-offset-x $service-icon-cancer-offset-y;
        width:$service-icon-cancer-width;
        height:$service-icon-cancer-height;
        &Hover {
            background-position: $service-icon-cancer-hover-offset-x $service-icon-cancer-hover-offset-y;
            width:$service-icon-cancer-hover-width;
            height:$service-icon-cancer-hover-height;
        }
        &Active {
            background-position: $service-icon-cancer-active-offset-x $service-icon-cancer-active-offset-y;
            width:$service-icon-cancer-active-width;
            height:$service-icon-cancer-active-height;
        }
    }
    &.service-icon_heart {
        background-position: $service-icon-heart-offset-x $service-icon-heart-offset-y;
        width:$service-icon-heart-hover-width;
        height:$service-icon-heart-hover-height;
        &Hover {
            background-position: $service-icon-heart-hover-offset-x $service-icon-heart-hover-offset-y;
            width:$service-icon-heart-hover-width;
            height:$service-icon-heart-hover-height;
        }
        &Active {
            background-position: $service-icon-heart-active-offset-x $service-icon-heart-active-offset-y;
            width:$service-icon-heart-active-width;
            height:$service-icon-heart-active-height;
        }
    }
    &.service-icon_blank {
        background-position: $service-icon-blank-offset-x $service-icon-blank-offset-y;
        width:$service-icon-blank-width;
        height:$service-icon-blank-height;
        &Hover {
            background-position: $service-icon-blank-hover-offset-x $service-icon-blank-hover-offset-y;
            width:$service-icon-blank-hover-width;
            height:$service-icon-blank-hover-height;
        }
        &Active {
            background-position: $service-icon-blank-active-offset-x $service-icon-blank-active-offset-y;
            width:$service-icon-blank-active-width;
            height:$service-icon-blank-active-height;
        }
    }
    &.service-icon_blankLife {
        background-position: $service-icon-blank-life-offset-x $service-icon-blank-life-offset-y;
        width:$service-icon-blank-life-width;
        height:$service-icon-blank-life-height;
        &Hover {
            background-position: $service-icon-blank-life-hover-offset-x $service-icon-blank-life-hover-offset-y;
            width:$service-icon-blank-life-hover-width;
            height:$service-icon-blank-life-hover-height;
        }
        &Active {
            background-position: $service-icon-blank-life-active-offset-x $service-icon-blank-life-active-offset-y;
            width:$service-icon-blank-life-active-width;
            height:$service-icon-blank-life-active-height;
        }
    }
    &.service-icon_menu {
        background-position: $service-icon-menu-offset-x $service-icon-menu-offset-y;
        width:$service-icon-menu-width;
        height:$service-icon-menu-height;
    }
}
