/*
#overview
Button

Icons

*/
button.btn,
input.btn {
    font-family: 'Hiragino Kaku Gothic ProN', Meiryo, MS-PGothic, sans-serif;
    appearance: none;
    cursor: pointer;
    &[disabled] {
        -webkit-text-fill-color: $gray-light;
        -webkit-opacity: 1;
    }
}
.btn {
    display: inline-block;
    font-size: 16px;
    line-height: 16px;
    padding: .75em 1em;
    text-decoration: none;
    text-align: center;
    vertical-align: baseline;
    border: 1px solid transparent;
    border-radius: $base-radius;
    box-sizing: border-box;
    transition: background-color .2s $standard-curve, color .2s $standard-curve;
    &:focus , &:hover {
        text-decoration: none;
    }
}

