    <button class="btnCircular btnCircular_next">
        <i aria-hidden="true" class="rex-icon chevron-right"></i>
    </button>
    <button class="btnCircular btnCircular_top" data-trigger="pageTop">
        <i aria-hidden="true" class="rex-icon chevron-top"></i>
    </button>
</div>

```

*/
button.btnCircular {
    cursor: pointer;
}
.btnCircular {
    position: relative;
    display: inline-block;
    width: 40px;
    height: 40px;
    color: $gray-light;
    background-color: transparent;
    border: 1px solid $gray-light;
    border-radius: 50%;
    box-sizing: border-box;
    transition: background-color .2s $standard-curve, color .2s $standard-curve;
    .rex-icon {
        position: absolute;
        top: -1px;
    }
    .rex-icon::before {
        line-height: 40px;
    }
    &:hover, &:focus {
        color: $life-primary;
        background-color: transparent;
        border: 1px solid $life-primary;
    }
    &:active {
        color: $life-darker;
        background-color: transparent;
        border: 1px solid $life-darker;
    }
}
.btnCircular_prev {
    .rex-icon {
        left: 6px;
    }
}
.btnCircular_next {
    .rex-icon {
        right: 6px;
    }
}
.btnCircular_top {
    .rex-icon {
        left: 7px;
    }
}

