.table-parent{
  margin-top: 30px;
  table{
    border-top: 2px solid $gray-light;
    border-spacing: 0;
    width: 100%;
    th{
      border-bottom: 2px solid $gray-light;
      padding: 15px 25px;
      font-weight: bold;
      vertical-align: top;
      text-align: left;
      width: 180px;
    }
    td{
      border-bottom: 2px solid $gray-light;
      padding: 15px 25px;
    }
    tr.bdNone th,
    tr.bdNone td{border-bottom: none;}
  }
}
.table-inner{
  margin-top: -5px;
  table{
    border-top: none;
    th,
    td{
      padding: 5px 0 0;
      border-bottom: none;
      font-weight: normal;
      vertical-align: top;
    }
    th{
      white-space: nowrap;
      width: 200px;
      font-weight: bold;
    }
    th+td{
      white-space: nowrap;
    }
    [colspan]{
      padding: 10px;
    }

  }

}

@media screen and (max-width: 756px){
    .table-parent table tr,
    .table-parent table th,
    .table-parent table td{
      display: block;
      width: auto;
    }
    .table-parent table th{
      border-bottom: none;
      padding: 10px 0 0;
    }
    .table-parent table td{
      padding: 0 0 10px;
    }
    .table-inner{
      margin-top: 0;
    }
    .table-inner tr{ margin-top: 10px;}
    .table-inner table th,
    .table-inner table td{
      padding: 0;
    }
    .table-inner table [colspan]{padding: 3px;}
}

