.paginate {
	text-align: center;
	margin-top: 40px;
  a {
    &:hover li {
      text-decoration: none;
    }
  }
  .paginate-btn {
    display: inline-block;
    position: relative;
    width: 30px;
    height: 42px;
    line-height: 38px;
    margin: 0 10px;
    text-align: center;
    vertical-align: middle;
  }
  strong {}
  span {
    display: inline-block;
    vertical-align: middle;
  }
  .paginate-btn_prev,.paginate-btn_next {
    border: 1px solid $brand-primary;
    border-radius: 50%;
    width: 42px;
    &.paginate-btn_disabled{
      border-color: $gray-light;
      color: $gray-light;
      .rex-icon{
        color: inherit;
      }
    }
  }
  a.paginate-btn_prev,a.paginate-btn_next {
    &:hover{
      background: $brand-primary;
        .rex-icon{
          color: $white;
        }
    }
  }
  .rex-icon{
  	vertical-align: middle;
  	color: $brand-primary;

  }
}
.paginateDisplay{
  text-align: right;
  margin-top: 20px;
}

.pageNextBox{
    border: 1px solid $gray;
    padding: 15px;
    margin-top: 30px;
    .pageNextBox-ttl{
        float: left;
        margin-right: 30px;
        color: $life-primary;
        .rex-icon{
            font-size: 16px;
            margin-left: 5px;
        }
    }
    .pageNextBox-txt{
        overflow: hidden;
        a{
            color: $gray-darkest;
        }
    }
}

@media screen and (max-width: 756px){
    .paginate .paginate-btn{
      width: 25px;
      margin: 0 5px;

    }
    .paginate .paginate-btn_next,
    .paginate .paginate-btn_prev{
      width: 42px;
    }
    .paginate{
      margin-top: 20px;
    }

    .pageNextBox{
      padding: 10px;
    }
    .pageNextBox .pageNextBox-ttl{
      margin-right: 10px;
    }
}

@media screen and (max-width: 320px){
    .paginate .paginate-btn{
        margin: 0 2px;
        font-size: 14px;
    }
    .paginate .paginate-btn_next, .paginate .paginate-btn_prev{
        width: 32px;
        height: 32px;
        line-height: 32px;
    }
    .paginate .rex-icon{
        font-size: 20px;
    }
}
