/* device */
.pcOnly{
  display: block!important;
}
img.pcOnly{
  display: inline-block!important;
}
.spOnly,img.spOnly{
  display: none!important;
}

/* font-family */
.font_serif{
  font-family:  " "," ", serif!important;
}

/* font-size */
// .fs10{}

/* color */
// .txt_red {}

/* Layout */
.layoutL{
    text-align: left!important;
}
.layoutC{
    text-align: center!important;
}
.layoutR{
    text-align: right!important;
}

/* margin */
//top
@for $i from 0 through 20 {
  .mt#{$i * 5} {
    margin-top:#{$i * 5}px!important;
  }
}
//bottom
@for $i from 0 through 20 {
  .mb#{$i * 5} {
    margin-bottom:#{$i * 5}px!important;
  }
}
//left
@for $i from 0 through 20 {
  .ml#{$i * 5} {
    margin-left:#{$i * 5}px!important;
  }
}
//right
@for $i from 0 through 20 {
  .mr#{$i * 5} {
    margin-right:#{$i * 5}px!important;
  }
}

.mAuto {
    margin: 0 auto!important;
}

/* padding */
//top
@for $i from 0 through 20 {
  .pt#{$i * 5} {
    padding-top:#{$i * 5}px!important;
  }
}
//bottom
@for $i from 0 through 20 {
  .pb#{$i * 5} {
    padding-bottom:#{$i * 5}px!important;
  }
}
//left
@for $i from 0 through 20 {
  .pl#{$i * 5} {
    padding-left:#{$i * 5}px!important;
  }
}
//right
@for $i from 0 through 20 {
  .pr#{$i * 5} {
    padding-right:#{$i * 5}px!important;
  }
}


/* background-color */
.bg_gray{
    background: #f7f7f7;
}

/* other */
.centerMessage{
    margin-top: 40px;
    text-align: center;
    margin-bottom: 15px;
}
.indentNum{
  margin-top: 10px;
  overflow: hidden;
  margin-bottom: 10px;
  .num{
    display: inline-block;
    float: left;
    margin-right: 10px;
  }
  .flTxt{
    overflow: hidden;
  }
}
.noticeIndent{
    font-size: 80%!important;
    text-indent: -1em;
    padding-left: 1em;
    display: inline-block;
}
.page-barba{display: none;}
#barba-wrapper{ }
.barba-container {position: relative;}
hr.bdGray{
  margin: 40px 0;
  background: #888;
}
.flick_modal{
    display: none;
}
@media screen and (max-width: 756px){
    /* device */
    .spOnly{
      display: block!important;
    }
    img.spOnly{
      display: inline-block!important;
    }
    .pcOnly,img.pcOnly{
      display: none!important;
    }
    /* font-size */
    .sp_min{
        font-size: 85%;
    }
    hr.bdGray{
      margin: 30px 0;
    }
    #barba-wrapper{
      width: 100vw;
    }
    .barba-container {
      width: 100vw;
    }
    .page-barba{display: block; overflow: hidden; position: absolute; left: 0; top: 100px; z-index: 118; width: 100%; height: 50px;}
    .page-barba li{width: 33.33%;height: 50px; float: left;}
    .page-barba li:last-child{ float: right;}
    .page-barba li a{ display: block; width: 100%; height: 100%; text-indent: -9999px;}
    // .page-barba.cur{ position:absolute; top: -100px;}
    // .page-barba.up{ position:fixed; top: 70px;}
    .barba-inner{width: 100vw;}



    .flick_modal{

        position: fixed;
        background: rgba(0,0,0,0.8);
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        z-index: 500;
    }
    .flick_modal_inner{
        padding-top: 240px;
        padding-left: 80px;
        padding-right: 80px;
        text-align: center;
        color: #fff;
        font-size: 32px;
    }
    .flick_img{
        margin-top: 20px;
    }
    .flick_img .img{
        background: url(../img/icon_swipe_arrow.png) no-repeat center top;
        padding-top: 20px;
        background-size: 100% auto;

    }

    .flick_img .img img {
        -webkit-animation:flick_anime 1.5s infinite linear alternate;
        animation:flick_anime 1.5s infinite linear alternate;
    }

    @-webkit-keyframes flick_anime {
        0% {-webkit-transform:translate(-10px, 0);}
        50% {-webkit-transform:translate(10px, 0);}
        100% {-webkit-transform:translate(-10px, 0);}
    }

    @keyframes flick_anime {
        0% {-webkit-transform:translate(-10px, 0);}
        50% {-webkit-transform:translate(10px, 0);}
        100% {-webkit-transform:translate(-10px, 0);}
    }
}


/* common parts */
/* gonin */
.gonin {
  position: relative;
  z-index: 1;
  margin: 1px 0 -36px;
  text-align: right;

  .gonin-txt {
    display: inline-block;
    padding: 8px 15px;
    font-size: 12px;
    color: #333;
    background: rgba($white, 0.8);
  }

  @media screen and (max-width: 756px) {
    margin: 0;
    text-align: left;

    .gonin-txt {
      width: auto;
      display: block;
      padding: 8px 5px;
      font-size: 10px;
      text-align: center;
      background: $gray-lightest;
    }
  }
}

.gonin_mv {
  position: absolute;
  top: 32px;
  right: 0;
  z-index: 1;

  @media screen and (max-width: 756px) {
    position: static;
  }

  .gonin {
    position: static;
    margin: 0;
    text-align: left;
  }
  
  .gonin-txt {
    width: auto;
    margin: 0;
    padding: 8px 20px;
    font-size: 12px;
    color: #333;
    background: rgba($white, 0.8);

    @media screen and (max-width: 756px) {
      padding: 8px 5px;
      font-size: 10px;
      text-align: center;
      background: #f7f7f7;
    }
  }
}

/* bobun */
.bobun {
  width: 1072px;
  margin: 50px auto 0;
  font-size: 14px;
  text-align: right;
  p {
    font-size: 14px;
  }

  @media screen and (max-width: 756px) {
    width: auto;

    &.bobun-sideMargin_sp {
      margin-left: 15px;
      margin-right: 15px;
    }
  }
}

/* suishouhoushin */
.suishouhoushin {
  .suishouhoushin-def {
    width: 890px;
    margin: 50px auto;
    font-size: 16px;
  }
  .suishouhoushin-term {
    margin-bottom: 10px;
    font-weight: 600;
    text-align: center;
  }
  .suishouhoushin-desc {

    .lnk {
      color: $lnk-txt;
      &:focus , &:hover {
          color: $lnk-txt-hover;
      }
      &:active {
          color: $lnk-txt-active;
      }
    }
    &:not(:first-child) {
      margin-top: 10px;
    }
  }

  @media screen and (max-width: 756px) {
    .suishouhoushin-def {
      width: auto;
      margin: 40px 0;
      font-size: 14px;
    }
    .suishouhoushin-desc {
      .lnk {
        font-size: 14px;
      }
    }
  }
}

/* hikiuke */
.hikiuke {
  .hikiukeBox {
    display: flex;
    justify-content: space-between;
    width: 1072px;
    margin: 50px auto;
    padding: 30px;
    font-size: 16px;
    border: 1px solid $gray-light;
    box-sizing: border-box;
  }
  .hikiukeBox-col {
    display: flex;
    width: 48%;
  }
  .hikiukeBox-def {
    display: flex;
    flex-direction: column;
  }
  .hikiukeBox-term {
    font-weight: 600;
  }
  .hikiukeBox-address {
    margin-top: auto;
  }

  @media screen and (max-width: 756px) {
    .hikiukeBox {
      display: block;
      width: auto;
      margin: 40px 0;
      padding: 15px;
      font-size: 14px;
    }
    .hikiukeBox-col {
      width: auto;
      &:not(:first-child) {
        margin-top: 20px;
      }
    }
    .hikiukeBox-def {
      display: block;
    }
