}

/* disclaimer */
.disclaimer {
  max-width: 890px;
  margin: 30px auto;
  .disclaimer-box {
    padding: 40px;
    color: $gray-medium;
    background-color: $gray-lightest;
  }
  .disclaimer-lstItem {
    position: relative;
    padding-left: 1em;
    font-size: 14px;
    line-height: 1.7;

    .disclaimer-lstStyle {
      position: absolute;
      left: 0;
    }
  }
  .disclaimer-date {
    margin-top: 8px;
    font-size: 14px !important;
    line-height: 1.7 !important;
    text-align: right;
  }

  @media screen and (max-width: 756px) {
    .disclaimer-box {
      padding: 15px;
    }
    .disclaimer-lstItem {
      font-size: 12px;
    }
    .disclaimer-date {
      font-size: 12px !important;
    }
  }
}

/* migrationMenu */
.migrationMenu {
  width: 1072px;
  margin: 80px auto;

  .migrationMenu-ttl {
    display: flex;
    font-weight: 500;
    margin: 22px 0 18px;
    padding-top: 30px;
    border-top: 1px solid $gray-light;

    .migrationMenu-ttlTxt {
      display: block;
      flex: 1 0 0%;
      font-size: 20px;
      line-height: 1.5;
    }

    &::before {
      content: "";
      display: block;
      width: 4px;
      margin-right: $base-space;
      background-color: $life-primary;
    }
    &:first-child {
      margin-top: 0;
      padding-top: 0;
      border-top: none;
    }
  }
  .migrationMenu-inr_product {
    display: flex;
    flex-wrap: wrap;

    * {
      line-height: 1.5;
    }
    .migrationMenu-card {
      display: flex;
      overflow: hidden;
      width: 342px;
      margin: 20px 0 0 20px;
      border: 1px solid $gray-light;
      border-radius: $base-radius;

      &:nth-child(3n+1) {
        margin-left: 0;
      }
      &:nth-child(-n+3) {
        margin-top: 0;
      }
      .migrationMenu-cardLnk {
        display: flex;
        width: 100%;
        color: $life-primary;

        @media screen and (min-width: 757px) {
          transition: color .2s $standard-curve;
          &:focus , &:hover {
              color: $lnk-txt-hover;
              text-decoration: none;
          }
          &:active {
              color: $lnk-txt-active;
          }
        }
      }
      .migrationMenu-cardImg {
        display: flex;
        align-items: center;
        background-color: $white;
        width: 104px;
        margin: 0;
        padding: 20px 18px;
        box-sizing: content-box;
        img {
          width: 100%;
          margin: 0 auto;
        }
      }
      .migrationMenu-cardDesc {
        display: flex;
        flex-direction: column;
        width: 170px;
        margin-top: 0;
        padding: 16px;
        background-color: $gray-lightest;
        box-sizing: content-box;
      }
      .migrationMenu-cardTtl {
        margin: 0 0 $base-space;
        font-size: 16px;
        color: $gray-darkest;
        .txt_bold {
          font-weight: bold;
        }
      }
      .migrationMenu-cardRead {
        margin-top: auto;
        text-align: right;
      }
    }
  }
  .migrationMenu-inr_column {
    display: flex;
    flex-wrap: wrap;

    * {
      line-height: 1.5;
    }
    .migrationMenu-card {
      display: flex;
      overflow: hidden;
      width: 194px;
      margin: 23px 0 0 23px;
      border: 1px solid $gray-light;
      border-radius: $base-radius;

      &:nth-child(5n+1) {
        margin-left: 0;
      }
      &:nth-child(-n+5) {
        margin-top: 0;
      }
    }
    .migrationMenu-cardLnk {
      display: flex;
      flex-direction: column;
      overflow: hidden;
      width: 100%;
      color: $life-primary;

      @media screen and (min-width: 757px) {
        transition: color .2s $standard-curve;
        &:focus , &:hover {
            color: $lnk-txt-hover;
            text-decoration: none;
        }
        &:active {
            color: $lnk-txt-active;
        }
      }
    }
    .migrationMenu-cardImg {
      width: 194px;
      height: 130px;
      margin: 0;
      img {
        max-width: 100%;
      }
    }
    .migrationMenu-cardTxt {
      margin-top: 0;
      padding: 10px;
      font-size: 14px;
      color: $gray-darkest;
      .txt_bold {
        font-size: 14px;
        font-weight: bold;
      }
    }
    .migrationMenu-cardRead {
      display: flex;
      justify-content: center;
      align-items: center;
      margin-top: auto;
      padding: 8px 16px;
      font-size: 14px;
      text-align: center;
      border-top: 1px solid $gray-light;

      &::before {
        content: "";
        display: block;
        width: 16px;
        height: 12px;
        margin-right: $base-space;
        background: url(/shared/img/icon_book.png) 50% 50% no-repeat;
        background-size: contain;
      }
    }
  }
  .migrationMenu-inr_category {
    * {
      line-height: 1.5;
    }
    .migrationMenu-lst {
      display: flex;
      flex-wrap: wrap;
    }
    .migrationMenu-btn {
      width: 250px;
      margin: 24px 0 0 24px;

      &:nth-child(4n+1) {
        margin-left: 0;
      }
      &:nth-child(-n+4) {
        margin-top: 0;
      }
    }
    .migrationMenu-btnLnk {
      display: flex;
      align-items: center;
      padding: 9px;
      text-decoration: none;
      color: $gray-darkest;
      background-color: $gray-lightest;
      border-radius: $base-radius;

      @media screen and (min-width: 757px) {
        transition: opacity .2s $standard-curve;

        &:focus , &:hover {
          opacity: 0.7;
        }
      }

      .migrationMenu-btnTxt {
        flex: 1 0 0%;
        margin-right: 12px;
        font-size: 16px;
      }
      &::before {
        content: "";
        display: block;
        width: 44px;
        height: 44px;
        margin-right: 12px;
        background-color: $white;
        background-position: 50% 50%;
        background-repeat: no-repeat;
        background-size: 29px auto;
        border-radius: 50%;
      }
      &.life::before { background-image: url(/shared/img/icon_life.png); }
      &.medical::before { background-image: url(/shared/img/icon_medical.png); }
      &.cancer::before { background-image: url(/shared/img/icon_cancer.png); }
      &.package::before { background-image: url(/shared/img/icon_package.png); }
      &.fire::before { background-image: url(/shared/img/icon_fire.png); }
      &.bicycle::before { background-image: url(/shared/img/icon_bicycle.png); }
      &.furniture::before { background-image: url(/shared/img/icon_furniture.png); }
      &.earthquake::before { background-image: url(/shared/img/icon_earthquake.png); }
      &.car::before { background-image: url(/shared/img/icon_car.png); }
      &.leisure::before { background-image: url(/shared/img/icon_leisure.png); }
      &.pet::before { background-image: url(/shared/img/icon_pet.png); }
      .rex-icon {
        margin-left: auto;
      }
    }
  }

  .bobun {
    margin-top: 30px;
    font-size: 14px;
    text-align: right;
    p {
      font-size: 14px;
    }
  }

  @media screen and (max-width: 756px) {
    width: auto;
    margin: 40px auto;

    .migrationMenu-ttl {
      margin: 35px 0 20px;
      padding-top: 0;
      border-top: none;

      .migrationMenu-ttlTxt {
        font-size: 18px;
      }
      &::before {
        margin-right: 12px;
      }
      &:first-child {
        margin-top: 0;
      }
    }
    .migrationMenu-inr_product {
      display: block;

      .migrationMenu-card {
        width: auto;
        margin: 12px 0 0 0;

        &:nth-child(-n+3) {
          margin-top: 12px;
        }
        &:first-child {
          margin-top: 0;
        }
        .migrationMenu-cardLnk {
          color: $life-primary;
        }
        .migrationMenu-cardImg {
          width: 32%;
          padding: 20px 10px;
          img {
            width: auto;
            max-width: 100%;
          }
        }
        .migrationMenu-cardDesc {
          width: inherit;
          flex: 1 0 0%;
        }
      }
    }
    .migrationMenu-inr_column {
      display: flex;
      flex-wrap: wrap;
      justify-content: space-between;

      * {
        line-height: 1.5;
      }
      .migrationMenu-card {
        width: 47.5%;
        margin: 4% 0 0 0;

        &:nth-child(-n+5) {
          margin-top: 4%;
        }
        &:nth-child(-n+2) {
          margin-top: 0;
        }
      }
      .migrationMenu-cardImg {
        width: auto;
        height: auto;
        margin: 0;
        img {
          width: 100%;
        }
      }
    }
    .migrationMenu-inr_category {
      .migrationMenu-lst {
        display: block;
      }
      .migrationMenu-btn {
        width: auto;
        margin: 12px 0 0 0;

        &:nth-child(4n+1) {
          margin-left: 0;
        }
        &:nth-child(-n+4) {
          margin-top: 12px;
        }
        &:first-child {
          margin-top: 0;
        }
      }
    }

    .bobun {
      margin-top: 20px;
    }
  }
}

@media screen and (max-width: 756px) {
  .migration-sideMargin_sp {
    margin-left: 15px;
    margin-right: 15px;
  }
}

