.phoneSupport {
  width: 1072px;
  margin: 80px auto;

  * {
    box-sizing: border-box;
  }

  @media screen and (max-width: 756px) {
    margin: 40px auto;
  }

  .phoneSupport-ttl {
    margin-bottom: 40px;
    font-size: 28px;
    font-weight: 600;
    text-align: center;

    &::after {
      content: "";
      display: block;
      width: 60px;
      height: 2px;
      margin: 10px auto 0;
      background-color: $life-primary;
    }
  }

  .phoneSupport-boxWrap {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    margin-top: -40px;
  }

  .phoneSupport-box {
    width: 48%;
    margin-top: 38px;
    padding: 20px 30px 30px;
    background-color: $gray-lightest;
    border-radius: $base-radius;

    &:nth-of-type(1) {
      width: 100%;
    }
  }

  .phoneSupport-boxTtl {
    margin-bottom: 15px;
    padding-bottom: 8px;
    font-size: 18px;
    font-weight: bold;
    border-bottom: 5px solid $white;

    .phoneSupport-boxTtlSub {
      display: inline-block;
      margin-left: 20px;
      font-size: 14px;
      font-weight: normal;
    }
  }
  
  .phoneSupport-boxNum {
    display: flex;
    align-items: center;
    font-size: 38px;
    font-weight: bold;
    color: $life-primary;

    .rex-icon {
      display: block;
      margin-right: 15px;
      font-size: 36px;
      color: $life-primary;
    }
  }
  
  .phoneSupport-boxTime {
    margin-top: 5px;
    font-size: 12px;
    color: $gray-dark;
  }

  .phoneSupport-boxCol {
    display: flex;
    justify-content: space-between;
  }

  .phoneSupport-boxTxt {
    width: 409px;
  }
}

.phoneSupport_sp {
  margin: 40px 0;

  .phoneSupport-ttl_sp {
    display: flex;
    font-weight: 500;
    margin: 35px 0 20px;

    .phoneSupport-ttlTxt_sp {
      display: block;
      flex: 1 0 0%;
      font-size: 18px;
      line-height: 1.5;
    }

    &::before {
      content: "";
      display: block;
      width: 4px;
      margin-right: 12px;
      background-color: $life-primary;
    }
    &:first-child {
      margin-top: 0;
    }
  }
  .callSupport .generalBtn .subTxt {
    padding: 4px 0 3px;
    color: #000;
    font-size: 12px;
  }
}