.normalTab{margin-top: 40px; }
.normalTab:after {
  content: "";
  clear: both;
  display: block;
}
.normalTab:first-child{ margin-top: 0;}
.normalTab .tabTitle{position: relative; z-index: 2;}
.normalTab .tabTitle>ul{ display: table; width: calc(100% + 20px); table-layout: fixed; border-collapse: separate; border-spacing: 10px 0px; margin-right: -10px; margin-left: -10px;}
.normalTab .tabTitle>ul>li { display: table-cell; text-align: center; vertical-align: middle; height: 50px; font-size: 26px; cursor: pointer;
border-radius:4px 4px 0 0;
border: 4px solid $brand-primary;border-bottom: none; color: #fff; background: $brand-primary;
position: relative;
}
.normalTab .tabTitle>ul>li.cur {border: 4px solid #e6e6e6; border-bottom: none; color: #000; background: #fff; font-weight: bold;}
.normalTab .tabTitle>ul>li a{ display: block; color: inherit; }
.normalTab .tabTitle>ul>li a span{ display: inline-block; position: relative;}
.normalTab .tabTitle>ul>li a span:after{
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  position: relative;
  display: inline-block;
  font: normal 24px rex-icon;
  content: "\F127";
  margin-left: 5px;
  vertical-align: middle;
 }
.normalTab .tabContents{z-index: 1;}
.normalTab .tabContents {  box-shadow: 0px -4px 0px 0px #e6e6e6;}
.normalTab .tabContents>ul>li { display: none; }
.normalTab .tabContents>ul>li.cur { display: block; }
.normalTab .tabContents>ul>li .tabContentsListInner { padding: 50px 0 20px; }

@media screen and (max-width: 756px){
    .normalTab{margin-top: 20px;}
    .normalTab .tabTitle{overflow: hidden;}
    .normalTab .tabTitle ul li{font-size: 18px;line-height: 1.25; height: 60px;}
    .normalTab .tabContents li .tabContentsListInner{padding: 20px 0;}
    .normalTab .tabTitle>ul>li a span:after{font-size: 18px;}
}
