/*
#styleguide
Link

```
<a href="#" class="lnk">
    
</a>
```
*/
.lnk {
	font-size: 16px;
    font-weight: 400;
    color: $lnk-txt;
    text-decoration: none;
    transition: color .2s $standard-curve;
    &:focus , &:hover {
        color: $lnk-txt-hover;
        text-decoration: none;
    }
    &:active {
        color: $lnk-txt-active;
    }
}

/*
#styleguide
Link - Arrow

```
<a href="#" class="lnk lnk_arrow">
    
    <i aria-hidden="true" class="rex-icon chevron-right"></i>
</a>
```
*/
.lnk_arrow {
    display: inline-flex;
    align-items: center;
    .rex-icon {
        font-size: 16px;
        line-height: inherit;
        margin-left: 8px;
    }
}

/*
#styleguide
Link - Top

```
<a href="#" class="lnk lnk_top" data-trigger="pageTop">
    
    <i aria-hidden="true" class="rex-icon arrow-up-l"></i>
</a>
```
*/
.lnk_top {
    display: inline-flex;
    align-items: center;
    .rex-icon {
        font-size: 16px;
        line-height: inherit;
        margin-left: 8px;
    }
}

/*
#styleguide
Link - Pdf

```
<a href="#" class="lnk lnk_pdf" target="_blank">
    PDF
    <i aria-hidden="true" class="rex-icon pdf-l"></i>
</a>
```
*/
.lnk_pdf {
    display: inline-flex;
