.columnRow{
    margin-right: -32px;
    margin-bottom: -32px;
    &:after{
        content: " ";
        clear: both;
        display: block;
        height: 2px;
    }
    .columnRowList{
        float: left;
        padding-right: 32px;
        padding-bottom: 32px;
        -moz-box-sizing: border-box;
        -webkit-box-sizing: border-box;
        -o-box-sizing: border-box;
        box-sizing: border-box;
    }
}

.columnRow_col2{
    .columnRowList{
        width: 50%;
    }
}

.columnRow_col3{
    .columnRowList{
        width: 33.33%;
    }
}

.columnRow_col4{
    .columnRowList{
        width: 25%;
    }
}

.columnRow_col3_4{
    .columnRowList{
        width: 25%;
        &:nth-child(1),
        &:nth-child(2),
        &:nth-child(3){
            width: 33.33%;
        }
    }
}

.columnRow_inline250{
    text-align: center;
    letter-spacing: -0.4;
    margin-right: -20px;
    .columnRowList{
        padding-right: 20px;
        letter-spacing: 0;
        float: none;
        display: inline-block;
        width: 250px;
        vertical-align: middle;
    }
    .columnRowList.long{
        width: 380px;
    }
}

.columnRow_inline200{
    text-align: center;
    letter-spacing: -0.4;
    margin-right: -60px;
    .columnRowList{
        padding-right: 60px;
        letter-spacing: 0;
        float: none;
        display: inline-block;
        width: 200px;
        vertical-align: middle;
    }
}

.columnRow_inline320{
    text-align: center;
    letter-spacing: -0.4;
    margin-right: -60px;
    .columnRowList{
        padding-right: 60px;
        letter-spacing: 0;
        float: none;
        display: inline-block;
        width: 320px;
        vertical-align: middle;
    }
}

.columnRow_inline300{
    text-align: center;
    letter-spacing: -0.4;
    margin-right: -60px;
    .columnRowList{
        padding-right: 20px;
        letter-spacing: 0;
        float: none;
        display: inline-block;
        width: 300px;
        vertical-align: middle;
    }
}

.columnRow_inline{
    text-align: center;
    letter-spacing: -0.4;
    margin-right: -60px;
    .columnRowList{
        padding-right: 60px;
        letter-spacing: 0;
        float: none;
        display: inline-block;
        vertical-align: middle;
    }
}

.columnRowWrap{
    overflow: hidden;
}
.columnRowWrap.bdT{
    padding-top: 20px;
    border-top: 1px solid $gray;
    margin-top: 20px;
}

.columnRow_ranking{
  counter-reset: rank;
  .columnRowList{
    width: 100%;
    .articleCard-img{
      float: left;
      width: 30%;
    }
    .articleCard{
      overflow: hidden;
      position: relative;
      &:before{
        position: absolute;
        left: 15px;
        top: 15px;
        z-index: 5;

        counter-increment:rank;
        content:counter(rank)"";
        width: 56px;
        height: 56px;
        line-height: 56px;
        text-align: center;
        border-radius: 50%;
        background: $brand-primary;
        color: $white;
        display: block;

      }
      .articleCard-inner{
        overflow: hidden;
      }
    }
  }
}

@media screen and (max-width: 756px){
    .columnRow{
        margin-bottom: -15px;
        margin-right: -15px;
    }
    .columnRow .columnRowList{
        width: 100%;
        padding-right: 15px;
        padding-bottom: 15px;
    }
    .columnRow_col2 .columnRowList{
        width: 100%;
    }
    .columnRow_col3_4 .columnRowList{
        width: 50%;
    }
    .columnRow_col3_4 .columnRowList:nth-child(1),
    .columnRow_col3_4 .columnRowList:nth-child(2),
    .columnRow_col3_4 .columnRowList:nth-child(3){
        width: 100%;
    }
    .columnRow_ranking .columnRowList .articleCard{

    }
    .columnRow_ranking .columnRowList .articleCard-img{
      height: 124px;
      width: 33%;

    }
    .columnRow_ranking .columnRowList .articleCard:before{
      width: 40px;
      height: 40px;
      line-height: 40px;
      top: 5px;
      left: 5px;
      font-size: 12px;
    }
    #column-main{
      width: 100%;
    }
    #column-side{
      width: 100%;
      margin-top: 40px;
    }
    #contents-inner{
      padding-bottom: 30px;
    }
    .columnRow_col4.columnRow_sp_col2 .columnRowList{
        width: 50%;
    }
    .columnRow_inline250 .columnRowList.long{
      width: 100%;
    }
    .columnRow_inline250 .circleListBox,
    .columnRow_inline300 .circleListBox{
        width: 100%;
    }
}

