/*Carousel------*/
.topBankArea {
    padding-top: 3px;
}
.bannerArea {
    margin: 0 auto;
    padding: 0;
    position: relative;
    text-align: center;
    width: 790px;
    border-top: none
}

.bannerArea a:hover img {
    opacity: .6;
    -ms-filter: "alpha(opacity=60)";
    -khtml-opacity: .6;
    -moz-opacity: .6
}

.carouselSec {
    background: transparent;
}

.carouselContainer {
    display: none
}
.carouselContainer.slick-initialized  {
    display: block;
}
.carouselArea ul {
    margin: 20px 0
}

.carouselArea ul li,
.carouselWrap ul li {
    float: left;
    margin-right: 10px;
    background-color: #fff;
    border: 1px solid #ebebeb;
    border-radius: 4px;
    -webkit-box-shadow: 0 5px 10px -5px rgba(0, 0, 0, .2);
    box-shadow: 0 5px 10px -5px rgba(0, 0, 0, .2)
}

.carouselArea ul.slick-dots li,
.carouselWrap ul.slick-dots li {
    border: none;
    margin-right: 5px;
    -webkit-box-shadow: none;
    box-shadow: none;
    font-size: 12px
}

.carouselArea ul li.mgr0 {
    margin-right: 0
}

.prWrapper {
    width: 790px;
    margin: 0 auto;
    padding-left: 12px;
    -webkit-box-sizing: border-box;
    -o-box-sizing: border-box;
    -ms-box-sizing: border-box;
    box-sizing: border-box
}

#CarouseContainer,
#CarouseContainer02,
#CarouseContainer03,
.carouselWrap {
    width: 790px
}

#CarouseContainer dl,
#CarouseContainer02 dl,
#CarouseContainer03 dl,
.carouselWrap dl {
    padding: 0
}

#CarouseContainer dl dt,
#CarouseContainer02 dl dt,
#CarouseContainer03 dl dt,
.carouselWrap dl dt {
    height: 154px;
    overflow: hidden;
    display: table-cell;
    text-align: center;
    vertical-align: top;
    overflow: hidden
}

#CarouseContainer dl dd,
#CarouseContainer02 dl dd,
#CarouseContainer03 dl dd,
.carouselWrap dl dd {
    padding: 0 15px 15px;
    text-align: center;
    line-height: 1.5;
    height: 120px;
    -webkit-box-sizing: border-box;
    box-sizing: border-box
}

#CarouseContainer dl dd p,
#CarouseContainer02 dl dd p,
#CarouseContainer03 dl dd p,
.carouselWrap dl dd p {
    font-size: 14px;
    text-align: left;
    color: #333
}

#CarouseContainer dl dd p.carouselProdName,
#CarouseContainer02 dl dd p.carouselProdName,
#CarouseContainer03 dl dd p.carouselProdName,
.carouselWrap dl dd p.carouselProdName {
    font-size: 12px;
    color: #828282;
    line-height: 1.2;
    text-align: left;
    height: 30px
}

#CarouseContainer dl dd p.pointPayOk,
#CarouseContainer02 dl dd p.pointPayOk,
#CarouseContainer03 dl dd p.pointPayOk,
.carouselWrap dl dd p.pointPayOk {
    font-size: 12px;
    color: #bf0000;
    line-height: 1.5;
    text-align: left;
    height: 1.5em;
    font-weight: bold;
}

#CarouseContainer dl dd .toDetail,
#CarouseContainer02 dl dd .toDetail,
#CarouseContainer03 dl dd .toDetail,
.carouselWrap dl dd .toDetail {
    text-align: right;
    color: #085b7c;
    position: relative;
    height: 22px;
}

#CarouseContainer dl dd .toDetail .lnkAttTxt,
#CarouseContainer02 dl dd .toDetail .lnkAttTxt,
#CarouseContainer03 dl dd .toDetail .lnkAttTxt,
.carouselWrap dl dd .toDetail .lnkAttTxt {
    position: absolute;
    top: 4px;
    left: 0;
    text-align: left;
    font-size: 10px;
    display: table-cell;
    height: 16px;
    vertical-align: middle;
    color: #4d4d4d;
    line-height: 1.2;
}

#CarouseContainer03 dl dd .toDetail .lnkAttTxt,
#sp-CarouselBox03 dl dd .toDetail .lnkAttTxt {
    top: -2px;
}

#CarouseContainer .slick-slide img,
#CarouseContainer02 .slick-slide img,
#CarouseContainer03 .slick-slide img,
.carouselWrap .slick-slide img {
    width: 100%;
    height: auto
}

#CarouseContainer a:focus,
#CarouseContainer02 a:focus,
#CarouseContainer03 a:focus,
.carouselWrap a:focus {
    outline: 0
}

#CarouseContainer a:hover,
#CarouseContainer02 a:hover,
#CarouseContainer03 a:hover,
.carouselWrap a:hover {
    text-decoration: none
}

.carouselHead {
    margin-top: 20px;
    font-size: 24px;
    line-height: 1.3;
    text-align: center;
}

.carouselHead .carouselHeadCap {
    display: inline-block;
    font-size: 12px;
    color: #4d4d4d
}

.slick-prev {
    position: absolute;
    top: 33%;
    left: -24px;
    width: 46px;
    height: 46px;
    background: url(/shared/img/ico_carousel_left.png);
    cursor: pointer;
    z-index: 10
}

.slick-next {
    position: absolute;
    top: 33%;
    right: -14px;
    width: 46px;
    height: 46px;
    background: url(/shared/img/ico_carousel_right.png);
    cursor: pointer;
    z-index: 10
}

@media screen and (max-width: 756px){
/*Carousel------*/
    .bannerArea {
        width: 100%;
        border-top: none;
        -webkit-box-sizing: border-box;
        -o-box-sizing: border-box;
        box-sizing: border-box;
        padding: 15px 10px;
        max-width: 500px
    }

    .bannerArea .slick-slide {
        margin-right: 7px
    }

    .bannerArea ul li img {
        width: 100%;
        height: auto
    }

    .bannerArea .prLogo {
        margin-bottom: 4px
    }

    .bannerArea .pr {
        color: #333;
        background: 0 0
    }

    .bannerArea .grdText {
        text-align: left;
        color: #686868;
        margin-top: 10px
    }

    .dotsContainer {
        margin-top: 15px
    }

    .carouselWrap ul li {
        margin-right: 10px;
        max-width: 190px;
    }

    .carouselWrap ul.slick-dots li {
        padding: 0 5px;
        font-size: 12px;
        margin-right: 0
    }

    .carouselWrap dl dd {
        padding: 0 13px 15px;
    }

    .topBankArea {
        display: none;
    }

    .carouselHead .carouselHeadCap {
        margin: 10px 3vw 4px;
    }

    .carouselWrap {
        width: 100%;
        padding-top: 24px;
        max-width: 380px;
        margin: 0 auto;
    }

    .carouselHead {
        margin-top: 40px;
    }
}

@media screen and (max-width: 320px){
    .carouselWrap dl dt {
        height: auto;
    }
    .carouselWrap dl dd {
        padding: 5px 8px 15px;
    }
    .carouselWrap dl dd p {
        font-size: 12px;
    }
    .carouselWrap dl dd p.carouselProdName {
    }
    .carouselWrap ul.slick-dots li {
        padding: 0 3px;
        font-size: 10px;
    }
}