.choice{
  border-top: 1px solid $gray;
  border-bottom: 1px solid $gray;
  padding: 30px 0;

}
.choice-ttl{
  font-size: 24px;
  margin-bottom: 10px;
  font-weight: bold;
}
.choice-tag{
  margin-bottom: 30px;
  .tagSort,.tag-parts{
    margin-right: 10px;
  }
}
.choice-select {
  overflow: hidden;
  width: 220px;
  text-align: center;
  select {
    width: 100%;
    padding-right: 1em;
    cursor: pointer;
    text-indent: 0.01px;
    text-overflow: ellipsis;
    border: none;
    outline: none;
    background: transparent;
    background-image: none;
    box-shadow: none;
    -webkit-appearance: none;
    appearance: none;
    font-size: 16px;
    &::-ms-expand {
      display: none;
    }
  }
  &.choice-select_01 {
    position: relative;
    border-radius: 2px;
    border: 1px solid $gray-medium;
    border-radius: 50px;
    background: #ffffff;
    &::before {
      position: absolute;
      top: 50%;
      right: 0.8em;
      margin-top: -3px;
      width: 0;
      height: 0;
      padding: 0;
      content: '';
      border-left: 6px solid transparent;
      border-right: 6px solid transparent;
      border-top: 6px solid $gray-medium;
      pointer-events: none;
    }
    select {
      padding: 8px 38px 8px 10px;
      color: $gray-medium;
    }
  }
}
