/*
#overview
Title


*/

/*
#styleguide
Title - h1
.ttlLv01 -block, 2, 
.ttlLv01-subCopy -element
.ttlLv01-mainCopy -element

```
<h1 class="ttlLv01"></h1>
<h1 class="ttlLv01">
    <br />
    
</h1>
<h1 class="ttlLv01">
    <span class="ttlLv01-subCopy"></span>
    <span class="ttlLv01-mainCopy"></span>
</h1>
```

*/
.ttlLv01 {
    font-size: 46px;
    font-weight: 600;
    line-height: 56px;
    text-align: center;
    &-subCopy {
        display: block;
        margin-bottom: 5px;
        font-size: 22px;
        line-height: 22px;
        color: $life-primary;
    }
    &-mainCopy {
        display: block;
    }
    &::after {
        display: block;
        content: "";
        width: 100px;
        height: 2px;
        margin: 13px auto 0;
        background-color: $life-primary;
    }
    &.ttlLv01_insurance {
        .ttlLv01-subCopy {
            color: $life-primary;
        }
        &::after {
            background-color: $life-primary;
        }
    }
}

/*
#styleguide
Title - h1 Insurance

```
<h1 class="ttlLv01 ttlLv01_insurance"></h1>
<h1 class="ttlLv01 ttlLv01_insurance">
    <br />
    
</h1>
<h1 class="ttlLv01 ttlLv01_insurance">
    <span class="ttlLv01-subCopy"></span>
    <span class="ttlLv01-mainCopy"></span>
</h1>
```

*/

/*
#styleguide
Title - h2
.ttlLv02 -block, 2, 
.ttlLv02-subCopy -element
.ttlLv02-mainCopy -element

```
<h2 class="ttlLv02"></h2>
<h2 class="ttlLv02">
    <br />
    
</h2>
<h2 class="ttlLv02">
    <span class="ttlLv02-subCopy">POINT 2</span>
    <span class="ttlLv02-mainCopy"></span>
</h2>
```

*/
.ttlLv02 {
    font-size: 32px;
    font-weight: 400;
    line-height: 40px;
    text-align: center;
    &-subCopy {
        display: block;
        margin-bottom: 4px;
        font-size: 16px;
        line-height: 16px;
        color: $life-primary;
    }
    &-mainCopy {
      display: block;
    }
}

/*
#styleguide
Title - h3
.ttlLv03 -block, 2, 
.ttlLv03-subCopy -element
.ttlLv03-mainCopy -element

```
<h3 class="ttlLv03"></h3>
<h3 class="ttlLv03">
    <br />
    
</h3>
<h3 class="ttlLv03">
    <span class="ttlLv03-subCopy"></span>
    <span class="ttlLv03-mainCopy">2,000</span>
</h3>
```

*/
.ttlLv03 {
    font-size: 24px;
    font-weight: 600;
    line-height: 32px;
    text-align: center;
    &-subCopy {
        display: block;
        margin-bottom: 4px;
        font-size: 16px;
        line-height: 16px;
    }
    &-mainCopy {
        display: block;
        color: $life-primary;
    }
}

/*
#styleguide
Title - h4

```
<h4 class="ttlLv04">()</h4>
<h4 class="ttlLv04">
    10<br />
    
</h4>
```

*/
.ttlLv04 {
    display: flex;
    align-items: stretch;
    font-size: 16px;
    font-weight: 300;
    line-height: 24px;
    &::before {
        display: block;
        content: "";
        width: 3px;
        margin-right: $base-space;
        background-color: $life-primary;
    }
}

/*
#styleguide
Title - h5

```
<h5 class="ttlLv05">3</h5>
<h5 class="ttlLv05">
    <br />
    
</h5>
```

*/
.ttlLv05 {
    font-size: 16px;
    font-weight: 600;
    line-height: 24px;
}

@media screen and (max-width: 756px){
    .ttlLv01 {
        font-size: 28px;
        font-weight: 600;
        line-height: 35px;
        text-align: center;
        &-subCopy {
            display: block;
            margin-bottom: 6.5px;
            font-size: 16px;
            line-height: 16px;
            color: $life-primary;
        }
        &-mainCopy {
            display: block;
        }
        &::after {
            display: block;
            content: "";
            width: 100px;
            height: 2px;
            margin: 6.5px auto 0;
            background-color: $life-primary;
        }
        &.ttlLv01_insurance {
            .ttlLv01-subCopy {
                color: $life-primary;
            }
            &::after {
                background-color: $life-primary;
            }
        }
    }

    .ttlLv02 {
        font-size: 24px;
        font-weight: 400;
        line-height: 32px;
        text-align: center;
        &-subCopy {
            display: block;
            margin-bottom: 2px;
            font-size: 16px;
            line-height: 16px;
            color: $life-primary;
        }
        &-mainCopy {
          display: block;
        }
    }

    .ttlLv03 {
        font-size: 16px;
        font-weight: 600;
        line-height: 24px;
        text-align: center;
        &-subCopy {
            display: block;
            margin-bottom: 4px;
            font-size: 12px;
            line-height: 12px;
        }
        &-mainCopy {
            display: block;
            color: $life-primary;
        }
    }

    .ttlLv04 {
        display: flex;
        align-items: stretch;
        font-size: 16px;
        font-weight: 300;
        line-height: 24px;
        &::before {
            display: block;
            content: "";
            width: 3px;
            margin-right: $base-space;
            background-color: $life-primary;
        }
    }

    .ttlLv05 {
        font-size: 16px;
        font-weight: 600;
        line-height: 24px;
    }
}
