/* ==================================================================

  Layout

================================================================== */
.main {
  width: 100%;
  margin: 0 auto;
  padding: 80px 0 80px;
}
.campaignList {
  padding: 32px 0 0;
  margin-bottom: 40px;
  background-color: $gray-lightest;
}
.campaignListInner {
  margin: 0 auto;
  width: 864px;
  .columnRow {
    margin-right: -26px;  
    .columnRowList {
    padding-right: 25px;

      .articleCard {
        box-shadow: none;
        border-radius: $base-radius;
        border: 1px solid $gray-light;
        background-color: $white;
        .choice-tag {
          margin-bottom: 15px;
        }
        .articleCard-inner {
          padding: 15px 15px 0;
          min-height: 180px;
          .articleCard-ttl {
            margin: 15px 0 10px;
            line-height: 1.4;
          }
          p {
            font-size: 14px;
          }
        }
        .articleCard-bottom {
            font-size: 14px;
            padding: 0 15px 10px;
            text-align: right;
          p {
            text-align: left;
            height: 23px;
          }
          .noPeriod {
            visibility: hidden;
          }
          .lnk {
            font-size: 14px;
          }
        }
      }
    }
  }
}

.mainTtl{
  @extend .ttlLv01;
  padding: 0 0 45px;
}

.mainTtl + .mainLead{
  margin-top: -50px;
  font-size: 20px;
  margin-bottom: 60px;
  text-align: center;

}
