/*
#styleguide
Breadcrumb

```
<div class="breadcrumb">
    <ul>
        <li><a href="/"></a></li>
        <li><span></span></li>
    </ul>
</div>
```
*/
.breadcrumb{
  padding: 15px;
  font-size: 12px;
  position: relative;
  z-index: 2;
  ul{
    overflow: hidden;
    max-width:1072px;
    margin: 0 auto;

    li{
      float: left;
      color: $gray;
      margin-right: 10px;
      a{
        color: inherit;
        padding-right: 10px;
      }
      &:after{
        color: inherit;
        font-family: rex-icon;
        content: "\F127";
        font-size: 10px;
        text-decoration: none;
      }
      &:last-child{
        &:after{
          display: none;
        }
      }
    }
  }
}

.breadcrumb + .visual,
.breadcrumb + .main{
  position: relative;
  margin-top: -50px;
  z-index: 1;

}

@media screen and (max-width: 756px){
    .breadcrumb+.main,
    .breadcrumb+.visual{
        margin-top: 0;
    }
    .breadcrumb{
      display: none;
    }
}

