/*
#styleguide
Address

```
<address class="address">
    <strong class="address-company"></strong><br />
    158-0094<br />
    141<br />
    <br />
    TEL: 0120-921-210<br />
    <a href="mailto:first.last@example.com" class="address-lnk">first.last@example.com</a>
</address>
```

*/
.address {
    font-size: 16px;
    font-weight: 400;
    line-height: 32px;
    font-style: normal;
}
.address-company {
    font-weight: bold;
}
.address-lnk {
    font-weight: 600;
    color: $lnk-txt;
    text-decoration: none;
    transition: color .2s $standard-curve;
    &:focus , &:hover {
        color: $lnk-txt-hover;
        text-decoration: none;
    }
    &:active {
        color: $lnk-txt-active;
    }
}
