.slideMenu {}
.slideMenu_icon {
    // max-width: 414px;
    margin: 0 auto;
    padding: 24px 0 24px 24px;
    text-align: center;
    background-color: #f2f2f2;
    .slideMenu-ttl {
        margin-bottom: 24px;
        color: $gray-darker;
        font-size: 24px;
        font-weight: 600;
        line-height: 24px;
    }
    .slideMenuLst {
      position: relative;
      display: flex;
      width: 100%;
      overflow-x: scroll;
       -ms-overflow-style:none;
      box-sizing: border-box;
       &::-webkit-scrollbar{
        display:none;
       }
    }
    .slideMenuLst-item {
        width: 88px;
        height: 120px;
        margin-right: 8px;
    }
    .slideMenuLst-itemHead {
        width: 88px;
        height: 80px;
        margin-bottom: 4px;
    }
    .slideMenuLst-itemBottom {}
    .slideMenuLst-itemTxt {
        color: $gray-darker;
        font-size: 14px;
        font-weight: 600;
        line-height: 18px;
    }
    .slideMenuLst-itemIcon {
        display: inline-block;
        background-image: url(/shared/img/icon/ico_service_sprite_slide_sp.png);
        background-size: 290px 290px;
        background-repeat: no-repeat;
        vertical-align: bottom;
        &_cancer {
            background-position: -220px 0;
            width: 70px;
            height: 70px;
        }
        &_box {
            background-position: -220px -70px;
            width: 70px;
            height: 70px;
        }
        &_bed {
            background-position: 0 -220px;
            width: 70px;
            height: 70px;
        }
        &_heart {
            background-position: -150px -70px;
            width: 70px;
            height: 70px;
        }
        &_dementia {
            background-image: url(/shared/img/icon/ico_service_sprite_slide2_sp.png);
            background-size: 220px 72px;
            background-position: 0 0;
            width: 70px;
            height: 70px;
        }
    }
    .slideMenuLst-itemLnk {
        outline: none;
        text-decoration: none;
    }
}
.slideIndicator {
    position: relative;
    height: 12px;
    margin-top: -20px;
    /* override for /sp/shared/scss/vendor/_slick.scss */
    .slick-dots {
        position: absolute;
        bottom: 0;
        opacity: 1;
        background: none;
        display: block;
        width: 100%;
        padding: 0;
        margin: 0;
        list-style: none;
        line-height: 1;
        text-align: center;
    }
    .slick-dots li {
        position: relative;
        display: inline-block;
        width: 4px;
        height: 4px;
        margin: -4px 2px 0 0;
        padding: 0;
        pointer-events: none;
        &:last-child {
            margin: 0;
        }
    }
    .slick-dots li button {
        font-size: 0;
        line-height: 0;
        display: block;
        width: 4px;
        height: 4px;
        padding: 0;
        cursor: pointer;
        color: transparent;
        border: 0;
        outline: none;
        background: transparent;
    }
    .slick-dots li button:hover,
    .slick-dots li button:focus {
        outline: none;
    }
    .slick-dots li button:hover:before,
    .slick-dots li button:focus:before {
        opacity: 1;
    }
    .slick-dots li button:before {
        font-family: 'slick';
        font-size: 6px;
        line-height: 20px;
        position: absolute;
        top: 0;
        left: 0;
        width: 4px;
        height: 4px;
        content: '';
        text-align: center;
        opacity: 1;
        color: transparent;
        -webkit-font-smoothing: antialiased;
        -moz-osx-font-smoothing: grayscale;
        border: 1px solid $brand-primary;
        border-radius: 2px;
        background-color: #f0c4c4;
        box-sizing: border-box;
    }
    .slick-dots li.slick-active button:before {
        opacity: 1;
        color: transparent;
        background-color: $brand-primary;
    }
}
