.lstToggle_order {
    counter-reset: num;
}
.lstToggle_order .lstToggle-descTermTtl::before {
    display: block;
    margin-bottom: 12px;
    counter-increment: num;
    content: counter(num,decimal);
    font-size: 80px;
    font-weight: 600;
    line-height: 80px;
    color: $life-primary;
}

