.lstAcc{
    border: 1px solid $gray-medium;
    padding: 15px 20px 10px;
    .lstAcc-parent{
        border-top: 1px solid $gray-medium;
        padding-top: 10px;
        &:first-child{
            border: none;
            padding-top: 0;
        }
        .lstAcc-parent-ttl{
            position: relative;
            margin-bottom: 10px;
            color: $gray-medium;
            &[aria-selected="true"] {
                color: $brand-primary;
                .lstAcc-descTermSwitchIcon {
                    transform: rotate(180deg);
                }
            }
            .lstAcc-descTermSwitchIcon {
                position: absolute;
                right: 0;
                top: 50%;
                margin-top: -13px;
                display: block;
                width: 24px;
                height: 24px;
                padding: 4px;
                background-color: $gray;
                border-radius: 12px;
                box-sizing: border-box;
                transition: transform .2s $standard-curve;
                .rex-icon {
                    font-size: 16px;
                    color: $white;
                    vertical-align: top;
                }
            }
        }
        .lstAcc-parent-con{
            margin-top: 10px;
        }
    }
    .lstAcc-child{}
    .lstAcc-child-ttl{
        color: $gray-medium;
        display: block;
        position: relative;
        padding-bottom: 10px;
        border-bottom: 1px solid $gray-medium;
        padding-top: 10px;
        &:last-child{
            border-bottom: none;
        }
        .lstAcc-descTermSwitchIcon {
            position: absolute;
            right: 0;
            top: 50%;
            margin-top: -9px;
            font-size: 16px;
            line-height: 16px;
            display: inline-block;
            color: $gray-medium;
        }
    }
}
.lstAcc-box{
    .lstAcc-box-con{
        padding-top: 30px;
    }
}
.lstAcc-box-ttl{
    position: absolute;
    right: 0;
    top: 70px;
    font-size: 16px;
    .lstAcc-descTermSwitch{
        display: inline-block;
        padding-right: 30px;
        color: $gray;
    }
    .lstAcc-descTermSwitchIcon {
        margin-left: 10px;
        display: inline-block;
        width: 24px;
        height: 24px;
        padding: 4px;
        background-color: $gray;
        border-radius: 12px;
        box-sizing: border-box;
        transition: transform .2s $standard-curve;
        .rex-icon {
            font-size: 16px;
            color: $white;
            vertical-align: top;
        }
    }
}
.lstAcc-box-wrap{
    position: relative;
    padding-bottom: 1px;
}

.logoBox{
    overflow: hidden;
    dt{
        float: left;
        margin-right: 30px;
        img{
            width: 100px;
            height: auto;
        }
    }
    dd{
        overflow: hidden;
        .logoBox-ttl{
            font-weight: bold;
            margin-top: 30px;
            &:first-child{
                margin-top: 0;
            }
            .normalTxt{
                font-weight: normal;
            }
        }
        .logoBox-txt{
            margin-top: 5px;
        }
        .logoBox-cat{
            margin-top: 5px;
            font-weight: bold;
            &:before{
                content:"";
            }
            span{
                display: inline-block;
            }
            span:after{
                content:"";
            }
            span:last-child:after{
                display: none;
            }
        }
        .logoBox-btn{
            margin-top: 10px;
            a{
                border: 1px solid $gray;
                display: inline-block;
                padding: 5px 15px;
                border-radius:4px ;
                color: $gray;
                text-decoration: none;
                &:hover{
                    background: $gray;
                    color: $white;
                }
            }
        }
    }
}

@media screen and (max-width: 756px){
    .lstAcc-box-wrap {
        margin-top: 20px;
    }
    .lstAcc-box-ttl{
        top: 15px;
    }
    .lstAcc-box-ttl .lstAcc-descTermSwitch{
        padding-right: 15px;
    }
    .lstAcc-box-wrap .lstAcc-descTermSwitch::before{
        display: none;
    }
    .lstAcc-box-ttl .lstAcc-descTermSwitchIcon{
        top: 26px;
        right: 15px;
        background: none;
    }
    .lstAcc-box-ttl .lstAcc-descTermSwitchIcon .rex-icon{
        color: $life-primary;
        font-size: 18px;
    }
}

