/*
#styleguide
Unordered List

```
<ul class="lst">
    <li class="lst-item"></li>
    <li class="lst-item">
        <ul class="lst">
            <li class="lst-item"></li>
            <li class="lst-item"></li>
        </ul>
    </li>
    <li class="lst-item"></li>
</ul>
```

*/
.lst {
	font-size: 16px;
    font-weight: 400;
    margin-top: 0;
    margin-bottom: 1em;
    padding-left: 2em;
    list-style: disc;
}
.lst .lst {
    margin-bottom: 0;
    list-style: circle;
}

/*
#styleguide
Unordered List - Notes

```
<ul class="lst lst_asterisk">
    <li class="lst-item"></li>
    <li class="lst-item"></li>
    <li class="lst-item"></li>
</ul>
```

*/
.lst_asterisk {
    list-style: none;
    padding-left: 0;
}
.lst_asterisk .lst-item {
    position: relative;
    padding-left: 24px;
}
.lst_asterisk .lst-item::before {
    position: absolute;
    top: 0;
    left: 0;
    display: block;
    content: "";
}

/*
#styleguide
Unordered List - Plain

```
<ul class="lst lst_plain">
    <li class="lst-item"></li>
    <li class="lst-item">
        <ul class="lst lst_plain">
            <li class="lst-item"></li>
            <li class="lst-item"></li>
        </ul>
    </li>
    <li class="lst-item"></li>
</ul>
```

*/
.lst_plain,
.lst_plain .lst_plain {
    list-style: none;
}

/*
#styleguide
Unordered List - Inline

```
<ul class="lst lst_inline">
    <li class="lst-item"></li>
    <li class="lst-item"></li>
    <li class="lst-item"></li>
</ul>
```

*/
.lst_inline,
.lst_inline .lst_inline {
    list-style: none;
    padding-left: 0;
}
.lst_inline .lst-item {
    display: inline-block;
    padding-left: 16px;
    &:first-child {
        padding-left: 0;
    }
}

/*
#styleguide
Ordered List

```
<ol class="lstOrder">
    <li class="lstOrder-item"></li>
    <li class="lstOrder-item">
        <ul class="lstOrder">
            <li class="lstOrder-item"></li>
            <li class="lstOrder-item"></li>
        </ul>
    </li>
    <li class="lst-item"></li>
</ol>
```

*/
.lstOrder {
    font-size: 16px;
    font-weight: 400;
    margin-top: 0;
    margin-bottom: 1em;
    padding-left: 2em;
    list-style: decimal;
}
.lstOrder .lstOrder {
    margin-bottom: 0;
    list-style: decimal;
}

/*
#styleguide
Ordered List - Notes

```
<ol class="lstOrder lstOrder_asterisk">
    <li class="lstOrder-item"></li>
    <li class="lstOrder-item"></li>
    <li class="lstOrder-item"></li>
</ol>
```

*/
.lstOrder_asterisk {
    list-style: none;
    padding-left: 0;
    counter-reset: num;
}
.lstOrder_asterisk .lstOrder-item {
    position: relative;
    padding-left: 32px;
}
.lstOrder_asterisk .lstOrder-item::before {
    position: absolute;
    top: 0;
    left: 0;
    display: block;
    counter-increment: num;
    content: "" counter(num,decimal);
}

/*
#styleguide
Descriptipn List - Vertical

```
<dl class="lstDesc">
    <dt class="lstDesc-term"></dt>
    <dd class="lstDesc-dfn"></dd>
</dl>
```

*/
.lstDesc {
    font: 400 16px/32px 'Hiragino Kaku Gothic ProN', Meiryo, MS-PGothic, sans-serif;
}
.lstDesc-term {
    font-weight: bold;
}

/*
#styleguide
Descriptipn List - Horizontal

```
<dl class="lstDesc lstDesc_horizon">
    <dt class="lstDesc-term"></dt>
    <dd class="lstDesc-dfn"></dd>
</dl>
```

*/
.lstDesc_horizon {
    display: flex;
}
.lstDesc_horizon .lstDesc-term {
    flex: none;
    margin-right: 16px;
}

