/*
#overview
Group Footer
*/

/*
#styleguide
Group Footer
*/
.pageTopWrap{
  .pageTopWrap-inner{
    max-width:1070px;
    margin: 0 auto;
    position: relative;
  }
  a{
    position: absolute;
    top: -60px;
    right: 0;
    display: inline-block;
    width: 40px;
    height: 40px;
    padding: 7px;
    color: #929292;
    background-color: transparent;
    border: 1px solid #929292;
    border-radius: 50%;
    box-sizing: border-box;
    transition: transform .2s $standard-curve;
    z-index:3;
    &:hover, &:focus {
        color: $life-primary;
        background-color: transparent;
        border: 1px solid $life-primary;
    }

  }
}

/*
#styleguide
Group Footer - Site Map
*/
/*  footer
---------------------------- */
#footerSiteMap01 {
  padding: 20px 0;
  text-align: left;
  background-color:$gray-lighter;
  font-family: 'Hiragino Kaku Gothic ProN', Meiryo, MS-PGothic, sans-serif;
}
#footerSiteMap01 a {
  color: $gray-darkest;
  text-decoration: none;
  &:hover {
      text-decoration: underline;
  }
}
#footerSiteMap01 .footerSiteMapIn {
    display: flex;
    justify-content: space-between;
    width: 1072px;
    margin: 0 auto;
    box-sizing: border-box;
}
#footerSiteMap01 .footerSiteMapIn-col {
    box-sizing: border-box;
    &:nth-child(1) {
        width: 240px;
    }
    &:nth-child(2) {
        width: 570px;
    }
    &:nth-child(3) {
        width: 240px;
    }
}
#footerSiteMap01 .footerSiteMapIn-inr {
    justify-content: space-between;

    @media screen and (min-width: 757px) {
      display: flex !important;
    }
}
#footerSiteMap01 .footerSiteMapInLst {
    box-sizing: border-box;
    &:nth-child(1) {
        width: 156px;
    }
    &:nth-child(2) {
        width: 170px;
    }
    &:nth-child(3) {
        width: 105px;
    }
    &:nth-child(4) {
        width: 150px;
    }
}
#footerSiteMap01 .footerSiteMapIn-cpn {
    margin-bottom: 10px;

    .rex-icon {
      display: none;
    }
}
#footerSiteMap01 .footerSiteMapIn-cpnLnk {
    font-size: 12px;
    font-weight: 600;
    span.pcOnly {
      display: inline !important;
    }
}
#footerSiteMap01 .footerSiteMapInLst-itemLnk {
    font-size: 12px;
    color: $gray-dark;
}
#footerSiteMap01 .footerSiteMapBottom {
  display: flex;
  flex-direction: column-reverse;
  width: 1072px;
  margin: 50px auto 0;
}
#footerSiteMap01 .copyright {
    text-align: center;
}
#footerSiteMap01 .copyright-txt {
    font-size: 10px;
    color: $gray-medium;
}
#footerSiteMap01 .footerSiteMapInUtilityLst {
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
}
#footerSiteMap01 .footerSiteMapInUtilityLst-item {
    margin: 0 12px;
}
#footerSiteMap01 .footerSiteMapInUtilityLst-itemLnk {
    font-size: 10px;
    color: $gray-dark;
}

@media screen and (max-width: 756px) {
  #footerSiteMap01 {
    padding: 0 15px 20px;
  }
  #footerSiteMap01 a {
    &:hover {
        text-decoration: none;
    }
  }
  #footerSiteMap01 .footerSiteMapIn {
      display: block;
      width: auto;
  }
  #footerSiteMap01 .footerSiteMapIn-col {
      border-bottom: 1px solid $gray-light;

      &:nth-child(1),
      &:nth-child(2),
      &:nth-child(3) {
          width: auto;
      }
  }
  #footerSiteMap01 .footerSiteMapIn-inr {
      display: block;
      padding-bottom: 15px;
  }
  #footerSiteMap01 .footerSiteMapInLst {
    &:nth-child(1),
    &:nth-child(2),
    &:nth-child(3),
    &:nth-child(4) {
        width: auto;
    }
  }
  #footerSiteMap01 .footerSiteMapIn-cpn {
      display: flex;
      justify-content: space-between;
      align-items: center;
      margin-bottom: 0;
      padding: 18px 0;

      .rex-icon {
        display: block;
        margin-left: auto;
        font-size: 20px;
        color: $gray-darkest;
        transition: all 0.3s ease;
        transform: rotate(90deg);
      }
      &[aria-selected=true] {
        .footerSiteMapIn-cpnLnk {
          color: $gray-darkest;
        }
        .rex-icon {
          color: $gray-darkest;
          transform: rotate(270deg);
        }
      }
  }
  #footerSiteMap01 .footerSiteMapIn-cpnLnk {
      margin-right: 10px;
      font-size: 16px;
      font-weight: 400;
      color: $gray-dark;
      pointer-events: none;
      span.pcOnly {
        display: none !important;
      }
  }
  #footerSiteMap01 .footerSiteMapInLst-item {
    margin-bottom: 5px;
  }
  #footerSiteMap01 .footerSiteMapInLst-itemLnk {
      font-size: 12px;
  }
  #footerSiteMap01 .footerSiteMapBottom {
      display: flex;
      flex-direction: column;
      width: auto;
      margin: 10px auto 0;
  }
  #footerSiteMap01 .copyright {
      text-align: left;
  }
  #footerSiteMap01 .copyright-txt {
      font-size: 10px;
  }
  #footerSiteMap01 .footerSiteMapUtility {
      margin-top: 15px;
  }
  #footerSiteMap01 .footerSiteMapInUtilityLst {
      justify-content: start;
  }
  #footerSiteMap01 .footerSiteMapInUtilityLst-item {
      margin: 0 15px 0 0;
  }
}

