.consumerBox{ border-radius: 4px;box-shadow: 0px 2px 7px -3px rgba(0,0,0,.5); padding: 15px; position: relative;}
.consumerBox-ttl{ font-size: 22px; font-weight: bold; padding-bottom: 5px;}
.consumerBox-txt{ font-size: 16px; font-weight: bold; padding-bottom: 12px;}
.consumerBox-btn{ border-top: 1px solid #ebebeb; font-weight: bold; padding-top: 12px; color: $brand-primary; text-align: center;}
.consumerBox-btn a{ color: $brand-primary; position: absolute; top: 0; right: 0; bottom: 0; left: 0; text-indent:-9999px; background:rgba(255,255,255,0);-webkit-tap-highlight-color:transparent;}
.consumerBox-btn a:hover{ background:rgba(255,255,255,0.4); }
.consumerBox-bottom{font-size: 14px; color: $brand-primary; text-align: center; font-weight: bold;}
.consumerBox-ttl_red{color: $brand-primary;}
.consumerBox-pop{
  display: none;
  position: absolute;
  left: 10px;
  bottom: 50px;
  right: 10px;
  background: $white;
  color: $gray-dark;
  border: 1px solid #ebebeb;
  border-radius: $base-radius;
  font-size: 14px;
  z-index:5;
}
.consumerBox-pop a{ display: block; padding: 8px; -webkit-tap-highlight-color:transparent; }
.consumerBox-pop a:hover{background: #f7f7f7; color: $brand-primary;}

.card-overlay{
  position: fixed;
  height: 100%;
  left: 0;
  top: 0;
  width: 100%;
  z-index: 3;
  cursor:pointer;
  -webkit-tap-highlight-color:transparent;
}

@media screen and (max-width: 756px){
    .consumerBox{padding: 10px;}
    .consumerBox-ttl{font-size: 16px;}
    .consumerBox-txt{font-weight: normal;}
}

